import { Component, Input } from '@angular/core';
import { trigger, style, animate, transition, query, stagger, keyframes, animateChild } from '@angular/animations';

import { PopupService, PopupPosition } from './../../../core/services/popup.service';

@Component({
  selector: 'itx-popup',
  templateUrl: './popup.component.html',
  styleUrls: ['./popup.component.scss'],
  animations: [
    trigger('fadeIn', [
      transition(
        ':enter',
        [animate('450ms 250ms ease-in-out', keyframes([
          style({ transform: 'translateY(-80%)', opacity: 0, offset: 0 }),
          style({ transform: 'translateY(10%)', opacity: 0.8, offset: 0.7 }),
          style({ transform: 'translateY(-5%)', opacity: 1, offset: 0.9 }),
          style({ transform: 'translateY(0)', opacity: 1, offset: 1 })
        ]))]
      ),
      transition(
        ':leave',
        [animate('350ms ease-in-out', keyframes([
          style({ opacity: 1, offset: 0 }),
          style({ opacity: 0, offset: 1 })
        ]))]
      )
    ]),
    trigger('animate', [
      transition(':enter', [ query('@*', stagger(50, [animateChild()]), { optional: true })]),
      transition(':leave', [ query(':leave', stagger(50, [animateChild()]), { optional: true })])
    ])
  ]
})
export class PopupComponent {

  @Input() public title: string;
  @Input() public text: string | boolean;

  public closeOnTap: boolean;
  public popupState: boolean;
  public bgState: boolean;
  public popupPosition: PopupPosition;
  public arrow: boolean;

  public btn: any;
  public btn2: any;
  public checkbox: any;
  public checkboxState: any;

  constructor(
    private popupService: PopupService
  ) {
    this.popupState = false;
    this.bgState = true;

    // Subscribe to the service who handle the state of the left menu - return a boolean
    this.popupService.popupState$.subscribe(
      (res) => {
        this.popupState = res.popupState;
        this.resetPopup();
        if (res.options) {
          if (res.options.closeOnTap != null) {
            this.closeOnTap = res.options.closeOnTap;
          } else {
            this.closeOnTap = true;
          }
          if (res.options.arrow != null) {
            this.arrow = res.options.arrow;
          } else {
            this.arrow = true;
          }
          if (res.options.title) { this.title = res.options.title; }
          if (res.options.text) { this.text = res.options.text; }
          if (res.options.btn) { this.btn = res.options.btn; }
          if (res.options.btn2) { this.btn2 = res.options.btn2; }
          if (res.options.checkbox) { this.checkbox = res.options.checkbox; }
          if (res.options.bgState !== null) { this.bgState = res.options.bgState; }
          if (res.options.position !== null) { this.popupPosition = res.options.position; }

          if (res.options.timer) {
            setTimeout(
              () => {
                this.popupService.setPopupState(false);
              },
              res.options.timer
            );
          }
        }
      }
    );
  }

  // Change the menu state from the service associated
  public closePopup(): void {
    if (this.popupState && this.closeOnTap) {
      this.popupService.setPopupState(!this.popupState);
    }
  }

  public resetPopup(): void {
    this.closeOnTap = null;
    this.title = null;
    this.text = null;
    this.btn = null;
    this.btn2 = null;
    this.checkbox = null;
    this.bgState = null;
    this.popupPosition = null;
  }

  public btnOnClick(btn: string): void {
    this.popupService.clickBtn(btn);
  }

  public checkboxChange(event: any): void {
    event.stopPropagation();
    this.popupService.setCheckboxState(event.target.checked);
  }

}
