import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/internal/Subject';
import { Observable } from 'rxjs/internal/Observable';
import { of } from 'rxjs/internal/observable/of';
import { ApiService } from './../api.service';
import { LangService } from './lang.service';
import { map } from 'rxjs/operators';

@Injectable()
export class RelationshipsService {
  public relationships: any[];
  private _relationships$: Subject<any> = new Subject();
  private translation: any;

  get relationships$(): Observable<any> {
    return this._relationships$.asObservable();
  }

  constructor(
    private apiService: ApiService,
    private langService: LangService
  ) {
    this.translation = this.langService.lang;
  }

  public getRelationships(): Observable<any> {
    if (this.relationships) {
      this._relationships$.next(this.relationships);
      return of(this.relationships);
    }
    return this.apiService.get('References/RelationshipTypes', { auth: false }).pipe(
        map((res) => {
            res.map(item => {
              item.Text = this.translation.enum.relationshipTypes[item.ID];
            });
            this.relationships = res;
            this._relationships$.next(this.relationships);
            return res;
          },
          (err) => {
            console.log('ERROR retrieving relationships', err);
            return of(err);
          }
        )
    );
  }

}
