import { Component, Input, OnInit, ElementRef, Renderer2 } from '@angular/core';
import * as exifjs from 'exif-js';

@Component({
 selector: 'itx-img-preview',
 templateUrl: './img-preview.component.html',
 styleUrls: ['./img-preview.component.scss']
})
export class ImgPreviewComponent implements OnInit {

  @Input() img: any;
  @Input() customClass: any;

  public imgEl: any;
  public canvas: any;

  constructor(
      private elRef: ElementRef,
      private renderer: Renderer2
  ) {

  }

  ngOnInit(): void {
    this.imgEl = this.renderer.createElement('img');
    this.canvas = this.renderer.createElement('canvas');
    this.imgEl.classList.add('img-preview');
    this.elRef.nativeElement.children[0].appendChild(this.imgEl);
    const imageSrc: any = new Image();
    imageSrc.onload  = (e) => {
      // Read EXIF
      this.renderImg(this.canvas, this.img, imageSrc).then((base64: string) => {
        this.renderer.setStyle(this.imgEl, 'background-image', 'url(' + base64 + ')');
      });
    };
    const reader: any = new FileReader();
    reader.onload = (e) => {
      imageSrc.src = e.target.result;
    };
    reader.readAsDataURL(this.img);
  }

  private renderImg(canvas: any, file: any, imgSrc: any): Promise<string> {
    return new Promise((resolve, reject) => {
      exifjs.getData(file, function() {
        const allMetaData: any = exifjs.getAllTags(this);
        if (Object.keys(allMetaData).length > 0) {
          const exifOrientation: number = allMetaData.Orientation;
          const ctx: any = canvas.getContext('2d');
          const width: number = allMetaData.PixelXDimension / 10;
          const height: number = allMetaData.PixelYDimension / 10;

          if (exifOrientation === 5
              || exifOrientation === 6
              || exifOrientation === 7
              || exifOrientation === 8
          ) {
            canvas.width = height;
            canvas.height = width;
          } else {
            canvas.width = width;
            canvas.height = height;
          }

          // transform context before drawing image
          switch (exifOrientation) {
            case 2:
              ctx.transform(-1, 0, 0, 1, width, 0);
              break;
            case 3:
              ctx.transform(-1, 0, 0, -1, width, height);
              break;
            case 4:
              ctx.transform(1, 0, 0, -1, 0, height);
              break;
            case 5:
              ctx.transform(0, 1, 1, 0, 0, 0);
              break;
            case 6:
              ctx.transform(0, 1, -1, 0, height, 0);
              break;
            case 7:
              ctx.transform(0, -1, -1, 0, height, width);
              break;
            case 8:
              ctx.transform(0, -1, 1, 0, 0, width);
              break;
            default:
              ctx.transform(1, 0, 0, 1, 0, 0);
          }

          // Draw img into canvas
          ctx.drawImage(imgSrc, 0, 0, width, height);
          resolve(canvas.toDataURL());
        }else {
          resolve(imgSrc.src);
        }
      });
    });
  }

}
