import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';

@Injectable()
export class CreateRequestService {

    private _createRequest$: Subject<boolean>;
    private _sendRequest$: Subject<any>;
    private _editRequestView$: Subject<any>;
    private _requestInProgress$: Subject<boolean>;

    constructor(
    ) {
        this._createRequest$ = new Subject();
        this._sendRequest$ = new Subject();
        this._editRequestView$ = new Subject();
        this._requestInProgress$ = new Subject();
    }

    // Subject send as observable
    get createRequest$(): Observable<boolean> {
        return this._createRequest$.asObservable();
    }

    get sendRequest$(): Observable<any> {
        return this._sendRequest$.asObservable();
    }

    get editRequestView$(): Observable<any> {
        return this._editRequestView$.asObservable();
    }

    get requestInProgress$(): Observable<boolean> {
        return this._requestInProgress$.asObservable();
    }

    // Change the state
    public createRequest(createRequest: boolean): void {
        this._createRequest$.next(createRequest);
    }

    // Send request
    public sendRequest(sendRequest: boolean, popup: boolean): void {
        this._sendRequest$.next({ sendRequest, popup });
    }

    public editRequestView(editRequestView: boolean, editRequest: boolean): void {
        this._editRequestView$.next({ editRequestView, editRequest });
    }

    public requestInProgress(inProgress: boolean): void {
        this._requestInProgress$.next(inProgress);
    }

}
