import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { map } from 'rxjs/operators';
import { ApiService } from './api.service';
import { LangService } from './references/lang.service';

@Injectable()
export class ProfileService {

    public profileRequests: any;
    public openProfileRequests: any;
    public closedProfileRequests: any;

    private _profileRequests$: Subject<any> = new Subject();
    private _profileRequestsById$: Subject<any> = new Subject();
    private _profileMessages$: Subject<any> = new Subject();
    private _openProfileRequests$: Subject<any> = new Subject();
    private _closedProfileRequests$: Subject<any> = new Subject();

    get profileRequests$(): Observable<any> {
        return this._profileRequests$.asObservable();
    }

    get profileRequestsById$(): Observable<any> {
        return this._profileRequestsById$.asObservable();
    }

    get profileMessages$(): Observable<any> {
        return this._profileMessages$.asObservable();
    }

    get openProfileRequests$(): Observable<any> {
        return this._openProfileRequests$.asObservable();
    }

    get closedProfileRequests$(): Observable<any> {
        return this._closedProfileRequests$.asObservable();
    }

    constructor(
        private apiService: ApiService,
        private langService: LangService
    ) {}

    public getProfileRequests(status?: string, offset?: number, limit?: number): void {
        const searchParams: any = {};
        if (status) { searchParams['status'] = status; }
        if (offset) { searchParams['offset'] = offset; }
        if (limit) { searchParams['limit'] = limit; }
        searchParams['languageCode'] = this.langService.userLang;
        this.apiService.get('Profile/Requests', { auth: true, search: searchParams }).subscribe(profileRequests => {
            this.profileRequests = profileRequests;
            this._profileRequests$.next(this.profileRequests);
        });
    }

    public getProfileRequestsById(id: number): void {
        let request: any[];
        const searchParams: any = {};
        searchParams['languageCode'] = this.langService.userLang;
        this.apiService.get('Profile/Requests/' + id, { auth: true, search: searchParams }).subscribe(
            (res) => {
                request = res;
                this._profileRequestsById$.next(request);
            },
            (err) => {
                console.log('ERROR retrieving profile request by ID:' + id + ' ', err);
            }
        );
    }

    public getOpenProfileRequests(offset?: number, limit?: number): void {
        const searchParams: any = {};
        searchParams['status'] = '0,1,2,3';
        if (offset) { searchParams['offset'] = offset; }
        if (limit) { searchParams['limit'] = limit; }
        searchParams['languageCode'] = this.langService.userLang;
        this.apiService.get('Profile/Requests', { auth: true, search: searchParams }).subscribe(openProfileRequests => {
            this.openProfileRequests = openProfileRequests;
            this._openProfileRequests$.next(this.openProfileRequests);
        });
    }

    public getClosedProfileRequests(status?: string, offset?: number, limit?: number): void {
        const searchParams: any = {};
        searchParams['status'] = '4,5';
        if (offset) { searchParams['offset'] = offset; }
        if (limit) { searchParams['limit'] = limit; }
        searchParams['languageCode'] = this.langService.userLang;
        this.apiService.get('Profile/Requests', { auth: true, search: searchParams }).subscribe(closedProfileRequests => {
            this.closedProfileRequests = closedProfileRequests;
            this._closedProfileRequests$.next(this.closedProfileRequests);
        });
    }

    public getProfileRequestsMessages(id: number): void {
        let messages: any[];
        const searchParams: any = {};
        searchParams['languageCode'] = this.langService.userLang;
        this.apiService.get('Profile/Requests/' + id + '/Messages', { auth: true, search: searchParams }).subscribe(
            (res) => {
                messages = res;
                this._profileMessages$.next(messages);
            },
            (err) => {
                console.log('ERROR retrieving profile request messages by ID:' + id + ' ', err);
            }
        );
    }

    public addProfileRequestsMessage(id: number, message: string, files?): Observable<any> {
        const _body: FormData = new FormData();
        if (files && files.length > 0) {
            let count: number = 0;
            files.forEach(file => {
                _body.append('File' + count, file);
                count++;
            });
        }
        _body.append('Message', JSON.stringify({Message: message}));
        const searchParams: any = {};
        searchParams['languageCode'] = this.langService.userLang;
        return this.apiService.post('Profile/Requests/' + id + '/Messages', { auth: true, search: searchParams, body: _body, contentType: 'multipart' }).pipe(
          map(
            (res) => res,
            (err) => {
                console.log('ERROR posting profile request messages' + id + ' ', err);
            }
          )
        );
    }

    public getAttachmentFile(requestId: number, filename: string): Observable<any> {
        const searchParams: any = {};
        searchParams['languageCode'] = this.langService.userLang;
        return this.apiService.get('Profile/Requests/' + requestId + '/AttachmentFiles/' + filename + '/', { auth: true, search: searchParams }).pipe(
          map(
            (res) => res,
            (err) => {
                console.log('ERROR gettin attachment file:', err);
            }
          )
        );
    }

    public getAttachmentFile64(requestId: number, filename: string): Observable<any> {
        const searchParams: any = {};
        searchParams['languageCode'] = this.langService.userLang;
        return this.apiService.get('Profile/Requests/' + requestId + '/AttachmentFiles64/' + filename + '/', { auth: true, search: searchParams, contentType: 'base64' }).pipe(
          map(
            (res) => {
              // IE < 12
              if (document['documentMode']) {
                res._body = res._body.slice(1, res._body.length - 1);
              }
              return res;
            },
            (err) => {
                console.log('ERROR gettin attachment file:', err);
            }
          )
        );
    }

    public getAdminPicture(): Observable<any> {
      return this.apiService.get('Profile/AdministratorPicture64', { auth: true, contentType: 'base64' }).pipe(
        map(
          (res) => {
            if (document['documentMode']) {
              res._body = res._body.slice(1, res._body.length - 1);
            }
            return res;
          },
          (err) => {
            console.log('ERROR gettin Admin Picture:', err);
          }
        )
      );
    }

}
