import { Component, HostListener } from '@angular/core';
import {
    trigger,
    state,
    style,
    animate,
    transition
} from '@angular/animations';
import { Router } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';

import { SessionService } from './../../../core/services/session.service';
import { LeftMenuService } from './../../../core/services/left-menu.service';
import { LangService } from './../../../core/services/references/lang.service';
import { UserConfigService } from '@app/modules/core/services/references/user-configuration.service';

@Component({
    selector: 'itx-left-menu',
    templateUrl: './left-menu.component.html',
    styleUrls: ['./left-menu.component.scss'],
    animations: [
        trigger('menuContent', [
            state('false', style({ opacity: 0 })),
            state('true', style({ opacity: 1 })),
            transition('0 => 1', animate('450ms 250ms ease-out')),
            transition('1 => 0', animate('250ms ease-in'))
        ]),
        trigger('menuState', [
            state('true', style({ left: 0 })),
            state('false', style({ left: '-100vw' })),
            transition('0 => 1', animate('250ms ease-out')),
            transition('1 => 0', animate('250ms 150ms ease-in'))
        ])
    ]
})
export class LeftMenuComponent {
    public translation;
    public companylogo: any;
    public openMenu = false;
    public LangList: any[];
    public userConfig: any;

    @HostListener('window:resize', ['$event'])
    private onResize(event: any): void {
        if (this.openMenu) {
            this.closeMenu();
        }
    }

    constructor(
        private leftMenuService: LeftMenuService,
        private sessionService: SessionService,
        private router: Router,
        private langService: LangService,
        private sanitizer: DomSanitizer,
        private userConfigService: UserConfigService
    ) {
        this.translation = this.langService.lang;
        this.userConfig = this.userConfigService.userconfig;
        // Subscribe to the service who handle the state of the left menu - return a boolean
        this.leftMenuService.openMenu$.subscribe(res => {
            this.openMenu = res;
        });

        this.getCompanyLogo();

        // Set the languages list values
        this.getLangList();
    }

    // Change the menu state from the service associated
    public closeMenu(): void {
        this.leftMenuService.openMenu(!this.openMenu);
    }

    public onClickLink(): void {
        if (window.innerWidth < 1024) {
            this.closeMenu();
        }
    }

    public getLangList(): void {
        this.LangList = [{ 'Name': 'English', 'ID': 1, 'refVal': 'en' },
        { 'Name': 'French', 'ID': 2, 'refVal': 'fr' }];
    }

    // Use the SessionService to disconnect the user
    public disconnect(): void {
        this.sessionService.logout().subscribe(res => {
            this.closeMenu();
            this.langService.getLang().subscribe(res => {
                this.translation = this.langService.lang;
            });
            this.router.navigate(['login']);
        });
    }

    public getCompanyLogo(): void {
        this.leftMenuService.getCompanyLogoUrl().subscribe(res => {
            this.companylogo = this.sanitizer.bypassSecurityTrustResourceUrl(
                'data:image/png;base64, ' + res._body
            );
            console.log(res);
        });
    }

    public onHelpClick(): void {
        const newTab = window.open(this.translation.leftMenu.help.url, '_blank');
        newTab.focus();
    }

}
