import { Injectable } from '@angular/core';
import { Observable, Subject, of} from 'rxjs';
import { flatMap, map } from 'rxjs/operators';
import cloneDeep from 'lodash-es/cloneDeep';

import { PayementMethod } from '@app/modules/shared/models/payementMethod';
import { ApiService } from './../api.service';
import { LangService } from '@services/references/lang.service';

@Injectable()
export class PaymentMethodsService {

    public paymentMethods: PayementMethod[];

    private _paymentMethods$: Subject<any> = new Subject();
    private translation: any;

    get paymentMethods$(): Observable<any> {
        return this._paymentMethods$.asObservable();
    }

    constructor(
        private apiService: ApiService,
        private langService: LangService
    ) {
      this.translation = this.langService.lang;
    }

    public getPaymentMethods(): Observable<any> {
        let paymentMethods: any;
        if (this.paymentMethods) {
            this._paymentMethods$.next(this.paymentMethods);
            return of(this.paymentMethods);
        } else {
            return this.apiService.get('References/PaymentMethods', { auth: false }).pipe(
              map(
                (res) => {
                    res.map(item => {
                      item.Text = this.translation.enum.paymentMethods[item.ID];
                    });
                    paymentMethods = res;
                    this.paymentMethods = paymentMethods;
                    this._paymentMethods$.next(paymentMethods);
                    return res;
                },
                (err) => {
                    console.log('ERROR retrieving paymentMethods', err);
                }
              )
            );
        }
    }

    public getPaymentMethodsById(id: number): Observable<any> {
        if (this.paymentMethods) {
            return of( cloneDeep(this.paymentMethods).find(el => el.ID === id) );
        } else {
            return this.getPaymentMethods().pipe(
              flatMap(res => {
                return this.getPaymentMethodsById(id);
              })
            );
        }
    }

}
