import { Component, Input } from '@angular/core';
import { Router } from '@angular/router';

import { CurrenciesService } from './../../../core/services/references/currencies.service';
import { ExpenseCategoriesService } from './../../../core/services/references/expense-categories.service';
import { LangService } from './../../../core/services/references/lang.service';

@Component({
    selector: 'itx-helpdesk-card',
    templateUrl: './helpdesk-card.component.html',
    styleUrls: ['./helpdesk-card.component.scss'],
    // changeDetection: ChangeDetectionStrategy.OnPush
})
export class HelpdeskCardComponent {

    public translation;

    @Input() public typeID: number;

    private fakeRequest = {
        Title: '',
        Date: '',
        Description: '',
        ID: null,
        Status: {
            ID: '',
            Name: '',
            Description: ''
        },
        Type: {
            ID: 1,
            Name: '',
            Description: ''
        },
        Priority: {
            ID: '',
            Name: '',
            Description: ''
        }
    };

    public _request: any = this.fakeRequest;

    @Input('request')
    public set request(val: any) {
        if (val.ID) {
            this._request = val;
            this.typeID = this._request.Type.ID;
            this.setIcon();
            this.setLabelAndClass();
            this.setType();
        } else {
            this._request = this.fakeRequest;
        }
    }

    public statusClass: string = 'shell';
    public statusLabel: string;
    public cardIcon: string;
    private statusClassArray: Array<string> = ['error', 'inprogress', 'closed'];

    public requestType: string;
    public isExpenseRequest: boolean = false;
    public isHelpdeskRequest: boolean = false;
    public isSuperiorRequest: boolean = false;

    // Expense var
    public expenseCurrency;
    public expenseCategory;

    constructor(
        private router: Router,
        private currenciesService: CurrenciesService,
        private expenseCategoriesService: ExpenseCategoriesService,
        private langService: LangService
    ) {
        this.translation = this.langService.lang;
    }

    public setIcon(): void {
        switch (this._request.Type.ID) {
            case 1:
                this.cardIcon = 'itx-icon-icon_autre';
                break;
            case 2:
                this.cardIcon = 'itx-icon-icon_adresses';
                break;
            case 3:
                this.cardIcon = 'itx-icon-icon_profil';
                break;
            case 4:
                this.cardIcon = 'itx-icon-icon_profil';
                break;
            case 5:
                this.cardIcon = 'itx-icon-icon_coeur';
                break;
            case 6:
                this.cardIcon = 'itx-icon-icon_profil';
                break;
            case 7:
                this.cardIcon = 'itx-icon-icon_profil';
                break;
            case 8:
                this.cardIcon = 'itx-icon-icon_frais';
                break;
            case 9:
                this.cardIcon = 'itx-icon-icon_frais';
                break;
            case 10:
                this.cardIcon = 'itx-icon-icon_job';
                break;
            case 11:
                this.cardIcon = 'itx-icon-icon_frais';
                break;
            default:
                this.cardIcon = 'itx-icon-icon_autre';
        }
    }

    public setLabelAndClass(): void {
        if (this._request.Status.ID === 1) {
            this.statusClass = 'created';
            this.statusLabel = this.translation.requestStatus.created || 'created';
        } else if (this._request.Status.ID === 2) {
            this.statusClass = 'pending';
            this.statusLabel = this.translation.requestStatus.pending || 'Pending';
        } else if (this._request.Status.ID === 3) {
            this.statusClass = 'inprogress';
            this.statusLabel = this.translation.requestStatus.inProgress || 'In Progress';
        } else if (this._request.Status.ID === 4) {
            this.statusClass = 'completed';
            this.statusLabel = this.translation.requestStatus.completed || 'Completed';
        } else if (this._request.Status.ID === 5) {
            this.statusClass = 'closed';
            this.statusLabel = this.translation.requestStatus.closed || 'Closed';
        } else {
            this.statusClass = 'shell';
        }
        console.log(this.translation.requestStatus);
    }

    public setType(): void {
        this.requestType = this._request.Type.Name;

        if (this.requestType === 'Expense') {
            this.isExpenseRequest = true;
            this.getExpenseDetail();
        } else if (this.requestType === 'UpdateSuperior') {
            this.isSuperiorRequest = true;
        } else {
            this.isHelpdeskRequest = true;
        }
    }

    public getExpenseDetail(): void {
        // Retrieve currency
        this.currenciesService.getCurrenciesByCode(this._request.ExpenseInfo.Currency).subscribe(res => {
            this.expenseCurrency = res;
        });

        // Retrieve category
        this.expenseCategoriesService.getExpenseCategoriesById(this._request.ExpenseInfo.Category).subscribe(res => {
            this.expenseCategory = res;
        });
    }

    public showRequestDetail(): void {
        if (this._request.ID !== null) {
            if (this.requestType === 'Expense') {
                this.router.navigate(['/edit-expense/' + this._request.ID]);
            } else if (this.requestType === 'Helpdesk') {
                this.router.navigate(['/edit-requests/' + this._request.ID]);
            } else {
                this.router.navigate(['/edit-profile-requests/' + this._request.ID]);
            }
        }
    }

}
