import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { FirstAuthComponent } from './modules/core/components/first-auth/first-auth.component';
import { RenewPasswordComponent } from './modules/core/components/renew-password/renew-password.component';
import { ChangePasswordComponent } from './modules/core/components/change-password/change-password.component';
import { ProxyRouteComponent } from './modules/core/components/proxy-route/proxy-route.component';

import { AuthGuard } from './modules/core/services/guards/auth-guard.service';
import { ApiLangGuard } from './modules/core/services/guards/api-lang-guard.service';
import { LoginComponent } from '@core/components/login/login.component';
import { ApiUserConfigGuard } from './modules/core/services/guards/api-user-config.service';

const routes: Routes = [
  {
    path: '',
    loadChildren: './modules/root/root.module#RootModule',
    canActivate: [AuthGuard, ApiLangGuard, ApiUserConfigGuard]
  },
  {
    path: 'login',
    component: LoginComponent,
    canActivate: [ApiLangGuard]
  },
  {
    path: 'first-auth',
    component: FirstAuthComponent,
    canActivate: [ApiLangGuard]
  },
  {
    path: 'renew-password',
    component: RenewPasswordComponent,
    canActivate: [ApiLangGuard]
  },
  {
    path: 'change-password',
    component: ChangePasswordComponent,
    canActivate: [ApiLangGuard]
  },
  {
    path: 'new-requests',
    component: ProxyRouteComponent,
    canActivate: [AuthGuard, ApiLangGuard],
    outlet: 'modal',
    children: [
      {
        path: '',
        loadChildren: './modules/helpdesk-new/helpdesk-new.module#HelpdeskNewModule',
      },
    ]
  },
  {
    path: 'upload-picture',
    component: ProxyRouteComponent,
    canActivate: [AuthGuard, ApiLangGuard],
    outlet: 'modal',
    children: [
      {
        path: '',
        loadChildren: './modules/upload-picture/upload-picture.module#UploadPictureModule',
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
