import { Directive } from '@angular/core';
import { NG_VALIDATORS, Validator, FormControl } from '@angular/forms';

@Directive({
  selector: '[itxRequiredBic]',
  providers: [
    { provide: NG_VALIDATORS, useExisting: BicValidatorDirective, multi: true },
  ]
})
export class BicValidatorDirective implements Validator {

  public static validate(c: FormControl): {[key: string]: any} {
    const bic: string = c.value;
    const invalidFormat: object = { 'invalid': true };
    const required: object = { 'required': true };
    if (bic === null || bic.length < 1) {
      return required;
    }
    if (bic.length < 8 || bic.length > 11) {
      return invalidFormat;
    }
    const regexpIllegalChar: RegExp = new RegExp('([a-zA-Z]{4})([a-zA-Z]{2})([a-zA-Z0-9]{2})([a-zA-Z0-9]{3})?');
    if (regexpIllegalChar.test(bic) === false) {
      return invalidFormat;
    }
    return null;
  }

  validate(c: FormControl): {[key: string]: any} {
    return BicValidatorDirective.validate(c);
  }
}
