import { Component, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { trigger, state, style, animate, transition } from '@angular/animations';

import { SessionService } from './../../services/session.service';
import { PopupService, PopupOptions } from './../../services/popup.service';
import { LangService } from './../../services/references/lang.service';

@Component({
  selector: 'itx-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.scss'],
  animations: [
    trigger('fade', [
        state('void', style({ opacity: 0 })),
        transition('void => *', animate('1s 150ms ease-out', style({ opacity: 1 })))
    ]),
    trigger('fade2', [
        state('void', style({ opacity: 0 })),
        transition('void => *', animate('350ms 350ms ease-out', style({ opacity: 1 }))),
        transition('* => void', animate('350ms ease-out', style({ opacity: 0 })))
    ]),
    trigger('slideDown', [
        state('false', style({ transform: 'translateY(0%)' })),
        state('true', style({ transform: 'translateY(8%)' })),
        transition('false => true', animate('350ms ease-out', style({ transform: 'translateY(8%)' }))),
        transition('true => false', animate('350ms 350ms ease-out', style({ transform: 'translateY(0)' })))
    ])
  ]
})
export class ChangePasswordComponent {
    public firstStep: boolean = true;

    public identifiant: FormControl;
    public oldPassword: FormControl;
    public newPassword: FormControl;
    public changePasswordForm: FormGroup;

    public onLoad: boolean;

    public translation;

    @ViewChild('inputOldPassword') inputOldPassword;
    @ViewChild('inputNewPassword') inputNewPassword;

    constructor(
        public fb: FormBuilder,
        private router: Router,
        private sessionService: SessionService,
        private popupService: PopupService,
        private langService: LangService
    ) {
        this.onLoad = false;

        this.identifiant = new FormControl(null, [ Validators.required ]);
        this.oldPassword = new FormControl(null, [ Validators.required ]);
        this.newPassword = new FormControl(null, [ Validators.required ]);
        this.changePasswordForm = this.fb.group({
            UserName: this.identifiant,
            Password: this.oldPassword,
            NewPassword: this.newPassword
        });

        this.translation = this.langService.lang;
    }

    public showOldPassword(): void {
        if (this.inputOldPassword.nativeElement.type === 'password') {
            this.inputOldPassword.nativeElement.type = 'text';
        } else {
            this.inputOldPassword.nativeElement.type = 'password';
        }
    }

    public showNewPassword(): void {
        if (this.inputNewPassword.nativeElement.type === 'password') {
            this.inputNewPassword.nativeElement.type = 'text';
        } else {
            this.inputNewPassword.nativeElement.type = 'password';
        }
    }

    public changePassword(): void {
        this.onLoad = true;
        this.sessionService.changePassword(this.changePasswordForm.value).subscribe(
            (res) => {
                const popupOptions: PopupOptions = {
                    title: this.translation.changePassword.popIn.title,
                    text: this.translation.changePassword.popIn.passwordChangeSuccess,
                    bgState: false,
                    arrow: false,
                    timer: 4000,
                    position: {
                        bottom: '4vh'
                    }
                };
                this.popupService.setPopupState(true, popupOptions);
                this.router.navigate(['/login']);
            },
            (err) => {
                this.onLoad = false;
                console.log('ERROR CHANGE PASSWORD', err);
                let errTxt: string;
                switch(err.Code) {
                    case 401003:
                    errTxt = this.translation.changePassword.popIn.usernameInvalid;
                    break;
                    case 401005:
                    errTxt = this.translation.changePassword.popIn.oldPasswordIncorrect;
                    break;
                    case 401006:
                    errTxt = this.translation.changePassword.popIn.passwordRequirements;
                    break;
                    case 500005:
                    errTxt = this.translation.changePassword.popIn.passwordChangeFailed;
                    break;
                }
                const popupOptions: PopupOptions = {
                    title: this.translation.changePassword.popIn.title,
                    text: errTxt,
                    bgState: false,
                    arrow: false,
                    timer: 4000,
                    position: {
                        bottom: '4vh'
                    }
                };
                this.popupService.setPopupState(true, popupOptions);
            }
        );
    }
}
