import { Component } from '@angular/core';
import { filter } from 'rxjs/operators';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { RouterService } from './../../../core/services/router.service';
import { SessionService } from './../../../core/services/session.service';
import { LeftMenuService } from './../../../core/services/left-menu.service';
import { CreateRequestService } from './../../../core/services/create-request.service';
import { LangService } from '@services/references/lang.service';

@Component({
  selector: 'itx-top-bar',
  templateUrl: './top-bar.component.html',
  styleUrls: ['./top-bar.component.scss']
})
export class TopBarComponent {

  public pageTitle: string;
  public goBackBtn: boolean;
  public goBackUrl: string;

  // TODO -> maybe a BehaviorSubject with the initial state directly in the service could be great
  private openMenu: boolean;
  public createRequestOpen: boolean;
  public editRequest: boolean;
  public translation: any;

  constructor(
    private routerService: RouterService,
    private sessionService: SessionService,
    private leftMenuService: LeftMenuService,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private createRequestService: CreateRequestService,
    private langService: LangService
  ) {
    this.openMenu = false;
    this.createRequestOpen = false;
    this.editRequest = false;
    this.translation = this.langService.lang;

    // Subscribe to the service who handle the state of the left menu - return a boolean
    // We always retrieve the state of the service, prevent issues with different state in UI and service
    this.leftMenuService.openMenu$.subscribe(
      (res) => {
        this.openMenu = res;
      }
    );

    this.createRequestService.createRequest$.subscribe(createRequest => {
      this.createRequestOpen = createRequest;
    });

    this.createRequestService.editRequestView$.subscribe(editRequest => {
      this.editRequest = editRequest.editRequestView;
    });

    // Subscribe to the router
    if (this.router.events) {
      this.router.events.pipe(
        filter(event => event instanceof NavigationEnd)
      ).subscribe(event => {
        this.getDataFromRoute();
      });
    }
  }

  // Change the menu state from the service associated
  public openLeftMenu(): void {
    this.leftMenuService.openMenu(!this.openMenu);
  }

  // Use the SessionService to disconnect the user
  public disconnect(): void {
    this.sessionService.logout().subscribe(res => {
      this.router.navigate(['login']);
    });
  }

  public close(): void {
    this.routerService.goBack();
  }

  private getDataFromRoute(): any {
    const route: ActivatedRoute = this.activatedRoute;
    this.getLastChildren(route);
  }

  private getLastChildren(route: ActivatedRoute): void {
    const children: ActivatedRoute[] = route.children;

    if (children.length === 0) {
      route.data.subscribe(data => {
        if (data) {
          if (data.title != null) {
            let title: string = data.title.split('.').reduce((o, i) => o[i], this.translation);
            if (title === undefined) {
              title = data.title;
            }
            this.pageTitle = title;
          }else {
            this.pageTitle = null;
          }

          this.goBackBtn = data.goBack;
        }

        if (data.goBack) {
          let url: string = this.router.url;
          url = url.replace(/[\/][a-z\-0-9]{1,}([\/][0-9]{1,})?$/, '');
          this.goBackUrl = url;
        }
      });
    } else {
      this.getLastChildren(children[0]);
    }
  }

}
