import { Injectable } from '@angular/core';
import { Observable, Subject, of} from 'rxjs';
import { flatMap, map } from 'rxjs/operators';
import cloneDeep from 'lodash-es/cloneDeep';

import { ApiService } from './../api.service';
import {LangService} from '@services/references/lang.service';

@Injectable()
export class CurrenciesService {

    public currencies: any[];

    private _currencies$: Subject<any> = new Subject();
      private translation: any;

    get currencies$(): Observable<any> {
        return this._currencies$.asObservable();
    }

    constructor(
        private apiService: ApiService,
        private langService: LangService
    ) {
      this.translation = this.langService.lang;
    }

    public getCurrencies(): Observable<any> {
        if (this.currencies) {
            const currenciesCopy = cloneDeep(this.currencies);
            this._currencies$.next(currenciesCopy);
            return of(currenciesCopy);
        } else {
            return this.apiService.get('References/Currencies', { auth: true }).pipe(
              map(
                (res) => {
                    res.map(item => {
                      item.Text = this.translation.enum.currencies[item.ID];
                    });
                    this.currencies = res;
                    this.transformCurrencyList();
                    const currenciesCopy = cloneDeep(this.currencies);
                    this._currencies$.next(currenciesCopy);
                    return res;
                },
                (err) => {
                    console.log('ERROR retrieving currencies', err);
                }
              )
            );
        }
    }

    public getCurrenciesByCode(code: string): Observable<any> {
        if (this.currencies) {
            return of( cloneDeep(this.currencies).find(el => el.Code === code));
        } else {
            return this.getCurrencies().pipe(
              flatMap(res => {
                return this.getCurrenciesByCode(code);
              })
            );
        }
    }

    private transformCurrencyList(): void {
        this.currencies.forEach(currency => {
            currency.Name = currency.DisplayName;
        });
    }

}
