import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from '@core/components/login/login.component';
import { FirstAuthComponent } from './components/first-auth/first-auth.component';
import { RenewPasswordComponent } from './components/renew-password/renew-password.component';
import { ChangePasswordComponent } from './components/change-password/change-password.component';
import { PageNotFoundPage } from './pages/page-not-found/page-not-found.page';

const routes: Routes = [
  {
    path: '',
    pathMatch: 'full',
    redirectTo: 'login'
  },
  {
    path: 'login',
    component: LoginComponent
  },
  {
    path: 'first-auth',
    component: FirstAuthComponent
  },
  {
    path: 'renew-password',
    component: RenewPasswordComponent
  },
  {
    path: 'change-password',
    component: ChangePasswordComponent
  },
  {
    path: '404',
    component: PageNotFoundPage
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CoreRoutingModule { }
