import { Injectable } from '@angular/core';
import { Router, CanActivate } from '@angular/router';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { UserConfigService } from '../references/user-configuration.service';

@Injectable()
export class ApiUserConfigGuard implements CanActivate {

    constructor(
        private router: Router,
        private userConfigService: UserConfigService
    ) {

    }

    public canActivate(): Observable<boolean> {
        if (this.userConfigService.userconfig) {
            return of(true);
        } else {
            return this.userConfigService.getConfig().pipe(
                map(translation => {
                    return true;
                })
            );
        }
    }
}
