import { Injectable } from '@angular/core';
import { Observable, Subject, of} from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { ApiService } from './api.service';
import { Gender } from '@app/modules/shared/models/gender';
import { LangService } from './references/lang.service';

export interface UserInfos {
    Picture: any;
    Birthplace: string;
    Nationality: NewNationalityObj;
    MaritalStatus: MaritalStatus;
    JobTitle: string;
    JobStartDate: string;
    Company: string;
    Country: NewNationalityObj;
    PersonalContactInfo: ContactInfo;
    ProfessionalContactInfo: ContactInfo;
    Administrator: Contact;
    Superior: any;
    EmergencyContact: Contact;
    BankAccounts: any[];
    Partner: any;
    Children: any;
    ID: number;
    Gender: Gender;
    FirstName: string;
    LastName: string;
    Birthdate: Date;
    Passport: any;
}

export interface NewNationalityObj {
  Code: String;
  DisplayName: String;
  Name: String;
}

export interface MaritalStatus {
    ID: number;
    Name: string;
    Description: string;
    Date: Date;
}

export interface Contact {
    Gender: any;
    FirstName: string;
    LastName: string;
    EmailAddress: string;
    PhoneNumber: string;
    Relationship: any;
}

export interface ContactInfo {
    Address: Address;
    EmailAddress: string;
    PhoneNumber: string;
    MobileNumber: string;
}

export interface Address {
    CareOf: string;
    Address: string;
    POBox: string;
    City: string;
    State: string;
    ZIP: string;
    Country: NewNationalityObj;
}

@Injectable()
export class UserService {

    public userInfos: UserInfos = null;

    private _userInfos$: Subject<UserInfos> = new Subject();
    private _userPic$: Subject<any> = new Subject();

    get userInfos$(): Observable<UserInfos> {
        return this._userInfos$.asObservable();
    }

    get userPic$(): Observable<any> {
      return this._userPic$.asObservable();
    }

    constructor(
        private apiService: ApiService,
        private langService: LangService
    ) {}

    public uploadPictureUser(picture: any): Observable<any> {
      let _body: any;
      if (picture) {
        _body = new FormData();
        _body.append('ProfilePicture', picture);
      }
      return this.apiService.post('Profile/ProfilePicture', { auth: true, body: _body, contentType: 'multipart'}).pipe(
        map(res => {
          return res;
        })
      );
    }

    public getPictureUser(): Observable<any> {
      return this.apiService.get('Profile/ProfilePicture64', { auth: true, contentType: 'base64' }).pipe(
        map(res => {
          this._userPic$.next(res);
          return res;
        })
      );
    }

    public getUserInfos(force: boolean = true): Observable<any> {
        if (this.userInfos != null && !force) {
            this._userInfos$.next(this.userInfos);
            return of(this.userInfos);
        } else {
          const searchParams: any = {};
          searchParams['languageCode'] = this.langService.userLang;
          return this.apiService.get('Profile/ProfileInfo', { auth: true, search: searchParams }).pipe(
              map(userInfos => {
                  this.userInfos = userInfos;
                  this._userInfos$.next(this.userInfos);
                  return userInfos;
              }),
              catchError(
                  err => of(err)
              )
            );
        }
    }

    public getSuperior(): Observable<any> {
        return this.apiService.get('Profile/Superior', { auth: true }).pipe(
          map(
            res => res
          )
        );
    }

    public changeSuperior(newSuperior: Contact): Observable<any> {
        return this.apiService.post('Profile/Superior', { auth: true, body: newSuperior }).pipe(
          map(
            res => res
          )
        );
    }

    public changeAddress(newAddress: any, files?: any): Observable<any> {
        const temp = {
            CareOf: newAddress.CareOf,
            Address: newAddress.Address,
            POBox: newAddress.POBox,
            City: newAddress.City,
            State: newAddress.State,
            ZIP: newAddress.ZIP,
            Country: newAddress.Country
        };
        const _body = new FormData();
        _body.append('AddressInfo', JSON.stringify(temp));
        if (files && files.length > 0) {
            let count = 0;
            files.forEach(file => {
                _body.append('File' + count, file);
                count++;
            });
        }
        const _search: any = {
            IsProfessionalAddress: newAddress.IsProfessionalAddress,
            StartDate: newAddress.StartDate
        };
        return this.apiService.post('Profile/Address', { auth: true, search: _search, body: _body, contentType: 'multipart' }).pipe(
          map(
            res => res
          )
        );
    }

    public changeContact(contactDetails: any): Observable<any> {
        const _body = contactDetails;
        return this.apiService.post('Profile/ContactDetails', { auth: true, body: _body }).pipe(
          map(
            res => res
          )
        );
    }

    public changePassport( passportData: any, personID: number, files?: any): Observable<any> {
        const _body: FormData = new FormData();
        _body.append('PassportInfo', JSON.stringify(passportData));
        if (files && files.length > 0) {
            let count = 0;
            files.forEach(file => {
                _body.append('File' + count, file);
                count++;
            });
        }
        const _search: any = {
            PersonID: personID
        };
        return this.apiService.post('Profile/Passport', { auth: true, body: _body, search: _search, contentType: 'multipart' }).pipe(
          map(
            res => res
          )
        );
    }

    public changeMaritalStatus(maritalStatus: any, partnerInfo?: any, files?: any): Observable<any> {
        const _body: FormData = new FormData();
        _body.append('MaritalStatusInfo', JSON.stringify(maritalStatus));
        if (partnerInfo) {
            _body.append('PartnerInfo', JSON.stringify(partnerInfo));
        }
        if (files && files.length > 0) {
            let count = 0;
            files.forEach(file => {
                _body.append('File' + count, file);
                count++;
            });
        }

        return this.apiService.post('Profile/MaritalStatus', { auth: true, body: _body, contentType: 'multipart' }).pipe(
          map(
            res => res
          )
        );
    }

    public addChild(newChild: any, files?: any): Observable<any> {
        const _body = new FormData();
        _body.append('ChildInfo', JSON.stringify(newChild));
        if (files && files.length > 0) {
            let count = 0;
            files.forEach(file => {
                _body.append('File' + count, file);
                count++;
            });
        }
        return this.apiService.post('Profile/AddChild', { auth: true, body: _body, contentType: 'multipart' }).pipe(
          map(
            res => res
          )
        );
    }

    public deleteChild(childID: number, reason: string): Observable<any> {
        const _search: any = {
            ChildID: childID,
            Reason: reason
        };
        return this.apiService.post('Profile/DeleteChild', { auth: true, search: _search }).pipe(
          map(
            res => res
          )
        );
    }

    public addBankAccount(newBank: any, files?: any): Observable<any> {
        const _body = new FormData();
        _body.append('BankAccountInfo', JSON.stringify(newBank));
        if (files && files.length > 0) {
            let count = 0;
            files.forEach(file => {
                _body.append('File' + count, file);
                count++;
            });
        }
        return this.apiService.post('Profile/AddBankAccount', { auth: true, body: _body, contentType: 'multipart' }).pipe(
          map(
            res => res
          )
        );
    }

    public deleteBankAccount(bankAccountID: number, reason: string): Observable<any> {
        const _search: any = {
            BankAccountID: bankAccountID,
            Reason: reason
        };
        return this.apiService.post('Profile/DeleteBankAccount', { auth: true, search: _search }).pipe(
          map(
            res => res
          )
        );
    }

}
