import { Injectable } from '@angular/core';
import { Observable, Subject, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { ApiService } from './../api.service';
import { LangService } from '@services/references/lang.service';

@Injectable()
export class BankAccountService {

    public bankAccountTypes: any[];

    private _bankAccountTypes$: Subject<any> = new Subject();
    private translation: any;

    get bankAccountTypes$(): Observable<any> {
        return this._bankAccountTypes$.asObservable();
    }

    constructor(
        private apiService: ApiService,
        private langService: LangService
    ) {
      this.translation = this.langService.lang;
    }

    public getBankAccountTypes(): Observable<any> {
        if (this.bankAccountTypes) {
            this._bankAccountTypes$.next(this.bankAccountTypes);
            return of(this.bankAccountTypes);
        }
        return this.apiService.get('References/BankAccountTypes', { auth: false }).pipe(
          map(
            (res) => {
              res.map(item => {
                item.Text = this.translation.enum.bankAccount[item.ID];
              });
              this.bankAccountTypes = res;
              this._bankAccountTypes$.next(this.bankAccountTypes);
              return res;
            },
            (err) => {
                console.log('ERROR retrieving bankAccountTypes', err);
                return of(err);
            }
          )
        );
    }

}
