import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { trigger, state, style, animate, transition } from '@angular/animations';
import { os } from 'platform';

import { SessionService, Credentials } from './../../services/session.service';
import { PopupService, PopupOptions } from './../../services/popup.service';
import { LangService } from './../../services/references/lang.service';

@Component({
    selector: 'itx-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss'],
    animations: [
        trigger('fade', [
            state('void', style({ opacity: 0 })),
            transition('void => *', animate('1s 150ms ease-out', style({ opacity: 1 })))
        ]),
        trigger('fade2', [
            state('void', style({ opacity: 0 })),
            transition('void => *', animate('350ms 350ms ease-out', style({ opacity: 1 }))),
            transition('* => void', animate('350ms ease-out', style({ opacity: 0 })))
        ]),
        trigger('slideDown', [
            state('false', style({ transform: 'translateY(0%)' })),
            state('true', style({ transform: 'translateY(8%)' })),
            transition('false => true', animate('350ms ease-out', style({ transform: 'translateY(8%)' }))),
            transition('true => false', animate('350ms 350ms ease-out', style({ transform: 'translateY(0)' })))
        ])
    ]
})
export class LoginComponent implements OnInit, OnDestroy {
    public identifiant: FormControl;
    public password: FormControl;
    public loginForm: FormGroup;

    public onLoad: boolean;

    public translation;

    @ViewChild('inputPassword') inputPassword;

    public deviceID;

    public invalidCredential = false;
    public noCredential = false;

    constructor(
        public fb: FormBuilder,
        private sessionService: SessionService,
        private router: Router,
        private popupService: PopupService,
        private langService: LangService
    ) {
        this.onLoad = false;

        this.identifiant = new FormControl(null, [Validators.required]);
        this.password = new FormControl(null, [Validators.required]);
        this.loginForm = this.fb.group({
            identifiant: this.identifiant,
            password: this.password
        });

        this.translation = this.langService.lang;

        this.deviceID = localStorage.getItem('deviceID');

        this.popupService.checkboxState$.subscribe(res => {
            if (res) {
                localStorage.setItem('checkPwaMsg', '1');
            } else {
                localStorage.setItem('checkPwaMsg', '0');
            }
        });
    }

    ngOnInit(): void {
        // Detect OS and show popup accordingly
        if (localStorage.getItem('checkPwaMsg') !== '1') {
            if (os.family === 'iOS' || os.family === 'Android') {
                this.showHomeScreenPopup();
            }
        }
    }

    ngOnDestroy(): void {
        this.popupService.setPopupState(false);
    }

    private showHomeScreenPopup(): void {
        let desc: string = this.translation.login.popIn.title.iOS;
        if (os.family === 'Android') {
            desc = this.translation.login.popIn.title.android;
        }
        const popupOptions: PopupOptions = {
            title: this.translation.login.popIn.title,
            text: desc,
            bgState: false,
            arrow: false,
            position: {
                bottom: '4vh'
            },
            checkbox: {
                checkboxState: true,
                checkboxLabel: this.translation.login.popIn.disableMessage
            }
        };
        this.popupService.setPopupState(true, popupOptions);
    }

    public generateNewDeviceID(): void {
        this.deviceID = this.sessionService.changeDeviceID();
    }

    public showPassword(): void {
        if (this.inputPassword.nativeElement.type === 'password') {
            this.inputPassword.nativeElement.type = 'text';
        } else {
            this.inputPassword.nativeElement.type = 'password';
        }
    }

    public login(): void {
        this.invalidCredential = false;
        if (this.loginForm.dirty && this.loginForm.valid) {
            this.onLoad = true;
            this.noCredential = false;
            const credentials: Credentials = { username: this.loginForm.value.identifiant, password: this.loginForm.value.password };

            this.sessionService.authenticate(credentials).subscribe(
                (res) => {
                    this.sessionService.saveSessionData(credentials, res);
                    this.langService.getLang(true).subscribe(res => {
                        this.translation = this.langService.lang;
                    });
                    this.router.navigate(['/']);
                },
                (err) => {
                    console.warn('LoginComponent.login() ERROR', err);
                    if (err.Code === 403001 || err.Code === 403002) {
                        this.sessionService.saveSessionData(credentials, err);
                        this.router.navigate(['/first-auth']);
                    } else if (err.Code === 401001) {
                        this.invalidCredential = true;
                        this.onLoad = false;
                    } else if (err.Code === 401002) {
                        this.router.navigate(['/change-password']);
                    }
                });
        } else {
            this.noCredential = true;
        }
    }

    public forgotPasswordHandler(): void {
        // new url to handle password recovery
        const url = 'https://mobile.datasharecenter.ch/recovery/';
        const newTab = window.open(url, '_blank');
        newTab.focus();
    }

    public helpHandler(url: string): void {
        const newTab = window.open(url, '_blank');
        newTab.focus();
    }
}
