import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { filter } from 'rxjs/operators';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';

@Injectable()
export class ModalService {

    public modalState: boolean;

    private _modalState$: Subject<boolean>;

    constructor(
        private router: Router,
        private activatedRoute: ActivatedRoute
    ) {
        this._modalState$ = new Subject();

        this.router.events.pipe(
          filter(events => events instanceof NavigationEnd)
        ).subscribe(event => {
            const childrensRoute: ActivatedRoute[] = this.activatedRoute.children;
            for (let i: number = 0; i < childrensRoute.length; i++) {
                const child: ActivatedRoute = childrensRoute[i];
                if (child.outlet !== 'modal' && this.modalState) {
                    this.router.navigate([{ outlets: { modal: null } }]);
                    this.modalState = !this.modalState;
                    this._modalState$.next(this.modalState);
                    return false;
                }
                if (child.outlet === 'modal' && !this.modalState) {
                    child.url.subscribe(url => {
                       this.router.navigate([{ outlets: { modal: url[0].path } }]);
                       this.modalState = !this.modalState;
                       this._modalState$.next(this.modalState);
                       return false;
                    });
                }
            }
        });
    }

    // Subject sent as observable
    get modalState$(): Observable<boolean> {
        return this._modalState$.asObservable();
    }

}
