import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { trigger, state, style, animate, transition } from '@angular/animations';

import { SessionService } from './../../services/session.service';
import { PopupService, PopupOptions } from './../../services/popup.service';
import { LangService } from './../../services/references/lang.service';

@Component({
    selector: 'itx-first-auth',
    templateUrl: './first-auth.component.html',
    styleUrls: ['./first-auth.component.scss'],
    animations: [
        trigger('fade', [
            state('void', style({ opacity: 0 })),
            transition('void => *', animate('1s 150ms ease-out', style({ opacity: 1 })))
        ]),
        trigger('fade2', [
            state('void', style({ opacity: 0 })),
            transition('void => *', animate('350ms 350ms ease-out', style({ opacity: 1 }))),
            transition('* => void', animate('350ms ease-out', style({ opacity: 0 })))
        ]),
        trigger('slideDown', [
            state('false', style({ transform: 'translateY(0%)' })),
            state('true', style({ transform: 'translateY(13%)' })),
            transition('false => true', animate('350ms ease-out', style({ transform: 'translateY(13%)' }))),
            transition('true => false', animate('350ms 350ms ease-out', style({ transform: 'translateY(0)' })))
        ])
    ]
})
export class FirstAuthComponent {
    public OTP: FormControl;
    public validAuthForm: FormGroup;

    public invalidOTP: boolean = false;
    public noOTP: boolean = false;

    public onLoad: boolean;
    public onResend: boolean;

    public userInfos;
    public translation;

    private _firstAuth: string;

    constructor(
        public fb: FormBuilder,
        private sessionService: SessionService,
        private router: Router,
        private popupService: PopupService,
        private langService: LangService
    ) {
        this.onLoad = false;
        this.onResend = false;
        this.translation = this.langService.lang;

        this.OTP = new FormControl(null, [ Validators.required ]);
        this.validAuthForm = this.fb.group({
            OTP: this.OTP
        });

        this._firstAuth = localStorage.getItem('_firstAuth');

        if (this._firstAuth !== '1') {
            this.sendActivationCode();
        } else {
            localStorage.setItem('_firstAuth', null);
        }

        this.sessionService.userInfos$.subscribe(res => {
            this.userInfos = res;
        });
        this.sessionService.getUserInfos();
    }

    public validAuth(): void {
        this.invalidOTP = false;
        if (this.validAuthForm.dirty && this.validAuthForm.valid) {
            this.onLoad = true;
            this.noOTP = false;
            this.sessionService.registerDevice(this.OTP.value).subscribe(
                (res) => {
                    const credentials = this.sessionService.restoreCredentials();
                    this.sessionService.saveSessionData(credentials, res);
                    setTimeout(() => {
                        this.langService.getLang(true).subscribe(res => {
                            this.translation = this.langService.lang;
                        });
                        localStorage.setItem('_firstAuth', null);
                        this.router.navigate(['/']);
                    }, 550);
                },
                (err) => {
                    console.log('ERROR registerDevice', err);
                    this.onLoad = false;
                    if (err.Code === 403003) {
                        this.invalidOTP = true;
                    }
                });
        } else {
            this.noOTP = true;
        }
    }

    public sendActivationCode(clicked?: boolean): void {
        localStorage.setItem('_firstAuth', '1');
        this.onResend = true;

        this.sessionService.sendEmailOTP().subscribe(res => {
            this.onResend = false;
            if (clicked) {
                const popupOptions: PopupOptions = {
                    title: this.translation.firstAuth.popIn.title,
                    text: this.translation.firstAuth.popIn.desc,
                    bgState: false,
                    arrow: false,
                    timer: 4000,
                    position: {
                        bottom: '4vh',
                        right: '0%',
                    }
                };
                this.popupService.setPopupState(true, popupOptions);
            }
        });
    }

    public onCloseClick(): void {
      localStorage.setItem('_firstAuth', null);
      this.router.navigate(['/login']);
    }
}
