import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { map } from 'rxjs/operators';
import { ApiService } from './api.service';
import { LangService } from './references/lang.service';

@Injectable()
export class HelpdeskService {

    public openRequests: Array<any> = [];
    public closedRequests: Array<any> = [];

    private _helpdesk$: Subject<any> = new Subject();
    private _closedHelpdesk$: Subject<any> = new Subject();
    private _helpdeskById$: Subject<any> = new Subject();
    private _helpdeskMessages$: Subject<any> = new Subject();
    private _closedRequests$: Subject<any> = new Subject();
    private _openRequests$: Subject<any> = new Subject();

    get helpdesk$(): Observable<any> {
        return this._helpdesk$.asObservable();
    }

    get helpdeskById$(): Observable<any> {
        return this._helpdeskById$.asObservable();
    }

    get helpdeskMessages$(): Observable<any> {
        return this._helpdeskMessages$.asObservable();
    }

    get closedRequests$(): Observable<any> {
        return this._closedRequests$.asObservable();
    }

    get openRequests$(): Observable<any> {
        return this._openRequests$.asObservable();
    }

    constructor(
        private apiService: ApiService,
        private langService : LangService
    ) {}

    public getHelpdeskRequests(status?: string, offset?: number, limit?: number): void {
        let requests: any[];
        const searchParams: any = {};
        if (status) { searchParams['status'] = status; }
        if (offset) { searchParams['offset'] = offset; }
        if (limit) { searchParams['limit'] = limit; }
        searchParams['languageCode'] = this.langService.userLang;
        this.apiService.get('Helpdesk/Requests', { auth: true, search: searchParams }).subscribe(
            (res) => {
                requests = res;
                // this.sortRequests(requests);
                this._helpdesk$.next(requests);
            },
            (err) => {
                console.log('ERROR retrieving helpdesk requests', err);
            }
        );
    }

    public getOpenHelpdeskRequests(offset?: any, limit?: number): void {
        let requests: any[];
        const searchParams: any = {};
        searchParams['status'] = '0,1,2,3';
        if (offset) { searchParams['offset'] = offset; }
        if (limit) { searchParams['limit'] = limit; }
        searchParams['languageCode'] = this.langService.userLang;
        this.apiService.get('Helpdesk/Requests', { auth: true, search: searchParams }).subscribe(
            (res) => {
                requests = res;
                this._openRequests$.next(requests);
            },
            (err) => {
                console.log('ERROR retrieving helpdesk requests', err);
            }
        );
    }

    public getClosedHelpdeskRequests(offset?: number, limit?: number): void {
        let requests: any[];
        const searchParams: any = {};
        searchParams['status'] = '4,5';
        if (offset) { searchParams['offset'] = offset; }
        if (limit) { searchParams['limit'] = limit; }
        searchParams['languageCode'] = this.langService.userLang;
        this.apiService.get('Helpdesk/Requests', { auth: true, search: searchParams }).subscribe(
            (res) => {
                requests = res;
                this._closedRequests$.next(requests);
            },
            (err) => {
                console.log('ERROR retrieving helpdesk requests', err);
            }
        );
    }

    public getHelpdeskRequestsById(id: number): void {
        let request: any[];
        const searchParams : any = {};
        searchParams['languageCode'] = this.langService.userLang;
        this.apiService.get('Helpdesk/Requests/' + id, { auth: true, search: searchParams }).subscribe(
            (res) => {
                request = res;
                this._helpdeskById$.next(request);
            },
            (err) => {
                console.log('ERROR retrieving helpdesk request by ID:' + id + ' ', err);
            }
        );
    }

    public getHelpdeskRequestsMessages(id: number): void {
        let messages: any[];
        this.apiService.get('Helpdesk/Requests/' + id + '/Messages', { auth: true }).subscribe(
            (res) => {
                messages = res;
                this._helpdeskMessages$.next(messages);
            },
            (err) => {
                console.log('ERROR retrieving helpdesk request messages by ID:' + id + ' ', err);
            }
        );
    }

    public getAttachmentFile(requestId: number, filename: string): Observable<any> {
        return this.apiService.get('Helpdesk/Requests/' + requestId + '/AttachmentFiles/' + filename + '/', { auth: true })
          .pipe(
            map(
              (res) => res,
              (err) => {
                  console.log('ERROR gettin attachment file:', err);
              }
            )
        );
    }

    public getAttachmentFile64(requestId: number, filename: string): Observable<any> {
        return this.apiService.get('Helpdesk/Requests/' + requestId + '/AttachmentFiles64/' + filename + '/', { auth: true, contentType: 'base64' }).pipe(
          map(
            (res) => {
              // IE < 12
              if (document['documentMode']) {
                res._body = res._body.slice(1, res._body.length - 1);
              }
              return res;
            },
            (err) => {
                console.log('ERROR gettin attachment file:', err);
            }
          )
        );
    }

    public addHelpdeskRequest(request, files?): Observable<any> {
        const _body: FormData = new FormData();
        if (files && files.length > 0) {
            let count: number = 0;
            files.forEach(file => {
                _body.append('File' + count, file);
                count++;
            });
        }
        _body.append('RequestInfo', JSON.stringify(request));
        return this.apiService.post('Helpdesk/Requests', { auth: true, body: _body, contentType: 'multipart' }).pipe(
            map(
                (res) => res,
                (err) => {
                    console.log('ERROR postin new helpdesk request:', err);
                }
            )
          );
    }

    public addHelpdeskRequestsMessage(id: number, message: string, files?): Observable<any> {
        let _body = new FormData();
        if (files && files.length > 0) {
            let count: number = 0;
            files.forEach(file => {
                _body.append('File' + count, file);
                count++;
            });
        }
        _body.append('Message', JSON.stringify({Message: message}));
        return this.apiService.post('Helpdesk/Requests/' + id + '/Messages', { auth: true, body: _body, contentType: 'multipart' })
          .pipe(map(
            (res) => res,
            (err) => {
                console.log('ERROR posting helpdesk request messages' + id + ' ', err);
            }
          )
        );
    }

    private sortRequests(requests: any[]): void {
        this.openRequests = [];
        this.closedRequests = [];
        requests.forEach(request => {
            if (request.Status.ID === 1 || request.Status.ID === 2 || request.Status.ID === 3) {
                this.openRequests.push(request);
            } else {
                this.closedRequests.push(request);
            }
        });
        this._openRequests$.next(this.openRequests);
        this._closedRequests$.next(this.closedRequests);
    }

}
