import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';

export enum API_STATE {
    IDLE,
    ON_OPERATIONS,
    UNREACHABLE
}

@Injectable()
export class ApiStateService {

    public apiState: API_STATE;
    private _ApiState$: Subject<API_STATE> = new Subject();

    get currentApiStateGet$(): Observable<API_STATE> {
        return this._ApiState$.asObservable();
    }

    public currentApiStateSet(apiState: API_STATE): void {
        this.apiState = apiState;
        this._ApiState$.next(this.apiState);
    }

}
