import { Injectable } from '@angular/core';
import { Observable ,  Subject ,  BehaviorSubject } from 'rxjs';

import { ApiService } from './api.service';

@Injectable()
export class RequestTypeService {

    private _requestType$: Subject<any> = new Subject();

    get requestType$(): Observable<any> {
        return this._requestType$.asObservable();
    }

    constructor(
        private apiService: ApiService
    ) {}

    public getRequestType() {
        let requestType;
        this.apiService.get('References/RequestStatus', { auth: false }).subscribe(
            (res) => {
                requestType = res;
                this._requestType$.next(requestType);
            },
            (err) => {
                console.log('ERROR retrieving RequestType', err);
            }
        );
    }

}
