import { Directive } from '@angular/core';
import { NG_VALIDATORS, Validator, FormControl } from '@angular/forms';

@Directive({
  selector: '[itxRequiredIban]',
  providers: [
    { provide: NG_VALIDATORS, useExisting: IbanValidatorDirective, multi: true },
  ]
})
export class IbanValidatorDirective implements Validator {

  private static IBAN_LENGTH: object = {
    AD: 24, AE: 23, AT: 20, AZ: 28, BA: 20, BE: 16, BG: 22, BH: 22, BR: 29,
    CH: 21, CR: 21, CY: 28, CZ: 24, DE: 22, DK: 18, DO: 28, EE: 20, ES: 24,
    FI: 18, FO: 18, FR: 27, GB: 22, GI: 23, GL: 18, GR: 27, GT: 28, HR: 21,
    HU: 28, IE: 22, IL: 23, IS: 26, IT: 27, JO: 30, KW: 30, KZ: 20, LB: 28,
    LI: 21, LT: 20, LU: 20, LV: 21, MC: 27, MD: 24, ME: 22, MK: 19, MR: 27,
    MT: 31, MU: 30, NL: 18, NO: 15, PK: 24, PL: 28, PS: 29, PT: 25, QA: 29,
    RO: 24, RS: 22, SA: 24, SE: 24, SI: 19, SK: 24, SM: 27, TN: 24, TR: 26
  };

  public static validate(c: FormControl): {[key: string]: any} {
    let iban: string = c.value;
    const invalidFormat: object = { 'invalid': true };
    const required: object = { 'required': true };
    if (iban === null || iban.length < 1) {
      return required;
    }
    iban = iban.replace(/\s+/g, '');
    if (iban.length < 15) {
      return invalidFormat;
    }
    const regexpIllegalChar: RegExp = new RegExp('[^a-zA-Z0-9]+$');
    if (regexpIllegalChar.test(iban) === true) {
      return invalidFormat;
    }
    const ibanExplode: Array<string> = iban.match(/^([a-zA-Z]{2})(\d{2})([a-zA-Z\d]+)$/);
    if (ibanExplode === null || iban.length !== IbanValidatorDirective.IBAN_LENGTH[ibanExplode[1]]) {
      return invalidFormat;
    }
    const ibanDigit: string = (ibanExplode[3] + ibanExplode[1] + ibanExplode[2]).replace(/[a-zA-Z]/g, (letter: string) => {
      return String(letter.charCodeAt(0) - 55);
    });
    if (IbanValidatorDirective.mod97(ibanDigit) !== 1) {
      return invalidFormat;
    }
    return null;
  }

  // https://stackoverflow.com/questions/929910/modulo-in-javascript-large-number
  private static mod97(data: string): number {
    let checksum: number = Number(data.slice(0, 2));
    let fragment: string = '';
    for (let offset: number = 2; offset < data.length; offset += 7) {
      fragment = String(checksum) + data.substring(offset, offset + 7);
      checksum = parseInt(fragment, 10) % 97;
    }
    return checksum;
  }

  validate(c: FormControl): {[key: string]: any} {
    return IbanValidatorDirective.validate(c);
  }



}
