import { Injectable } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable, Subject } from 'rxjs';

export interface PopupOptions {
    closeOnTap?: boolean;
    arrow?: boolean;
    timer?: number;
    title?: string | boolean;
    text?: string | boolean;
    btn?: {
        btnState?: boolean;
        btnLabel?: string;
        btnAction?: string;
    };
    btn2?: {
        btnState?: boolean;
        btnLabel?: string;
        btnAction?: string;
    };
    checkbox?: {
        checkboxState?: boolean;
        checkboxLabel?: string;
    };
    bgState?: boolean;
    position?: PopupPosition;
    arrowPosition?: any;
}

export interface PopupPosition {
    top?: string;
    bottom?: string;
    left?: string;
    right?: string;
}

@Injectable()
export class PopupService {

    public popupState: boolean;
    public checkboxState: boolean;

    private _popupState$: Subject<any>;
    private _btnClick$: Subject<any>;
    private _checkboxState$: Subject<boolean>;

    constructor(
        private router: Router,
        private activatedRoute: ActivatedRoute
    ) {
        this._popupState$ = new Subject();
        this._btnClick$ = new Subject();
        this._checkboxState$ = new Subject();
    }

    // Subject sent as observable
    get popupState$(): Observable<any> {
        return this._popupState$.asObservable();
    }

    get btnClick$(): Observable<any> {
        return this._btnClick$.asObservable();
    }

    get checkboxState$(): Observable<boolean> {
        return this._checkboxState$.asObservable();
    }

    public setPopupState(popupState: boolean, options?: PopupOptions): void {
        this.popupState = popupState;
        this._popupState$.next({ popupState, options });
    }

    public clickBtn(btn: string): void {
        this._btnClick$.next(btn);
    }

    public setCheckboxState(checkboxState: boolean): void {
        this.checkboxState = checkboxState;
        this._checkboxState$.next(checkboxState);
    }

}
