import { Injectable } from '@angular/core';
import { Observable ,  Subject ,  BehaviorSubject } from 'rxjs';

import { Gender } from '../../../shared/models/gender';
import { ApiService } from './../api.service';
import { LangService } from './lang.service';

@Injectable()
export class GendersService {

    public genders: Gender[];

    private _genders$: Subject<any> = new Subject();
    private translation: any;

    get genders$(): Observable<any> {
        return this._genders$.asObservable();
    }

    constructor(
        private apiService: ApiService,
        private langService: LangService
    ) {
      this.translation = this.langService.lang;
    }

    public getGenders(): void | boolean {
        if (this.genders) {
            this._genders$.next(this.genders);
            return false;
        }
        this.apiService.get('References/Genders', { auth: false }).subscribe(
            (res) => {
                res.map(item => {
                  item.Text = this.translation.enum.genders[item.ID];
                });
                this.genders = res;
                this._genders$.next(this.genders);
            },
            (err) => {
                console.log('ERROR retrieving genders', err);
            }
        );
        }

}
