import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';


/*
 * Form customs validator
 */

// Private regex to check email address
const EMAIL_REGEXP = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

export function validateEmail(c: FormControl): Observable<Promise<{}>> {
  // tslint:disable-next-line: max-line-length
  const value = EMAIL_REGEXP.test(c.value) ? null : { validateEmail: { valid: false } };

  return new Observable((obs: any) => {
      obs.next(value);
      obs.complete();
  });
}
