import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { Priority } from '../../../shared/models/priority';
import { ApiService } from './../api.service';
import { LangService } from './lang.service';

@Injectable()
export class PriorityLevelsService {

    public priorityLevels: Priority[];

    private _priorityLevels$: Subject<any> = new Subject();
    private translation: any;

    get priorityLevels$(): Observable<any> {
        return this._priorityLevels$.asObservable();
    }

    constructor(
        private apiService: ApiService,
        private langService: LangService,
    ) {
      this.translation = this.langService.lang;
    }

    public getPriorityLevels() {
        let priorityLevels: any;
        if (this.priorityLevels) {
            this._priorityLevels$.next(this.priorityLevels);
            return false;
        }
        this.apiService.get('References/PriorityLevels', { auth: false }).subscribe(
            (res) => {
                res.map(item => {
                  item.Text = this.translation.enum.priority[item.ID];
                });
                priorityLevels = res;
                this.priorityLevels = priorityLevels;
                this._priorityLevels$.next(priorityLevels);
            },
            (err) => {
                console.log('ERROR retrieving priorityLevels', err);
            }
        );
        }

}
