import { Component, OnInit } from '@angular/core';
import { trigger, state, style, animate, transition, animateChild } from '@angular/animations';

import { RouterService } from '@services/router.service';
import { ModalService } from '@services/modal.service';
import { CreateRequestService } from '@services/create-request.service';
import { UserConfigService } from '@app/modules/core/services/references/user-configuration.service';
import { ButtonService } from '../../services/button.service';

@Component({
  selector: 'itx-bottom-bar',
  templateUrl: './bottom-bar.component.html',
  styleUrls: ['./bottom-bar.component.scss'],
  animations: [
    trigger('slideIn', [
      state('void', style({ bottom: '-100%' })),
      transition('void => *', [animate('750ms 350ms ease-out', style({ bottom: 0 })), animateChild()])
    ])
  ]
})
export class BottomBarComponent implements OnInit{

  public currentUrl: string;
  public modalState: boolean;
  public createRequest: boolean;
  public editRequest: boolean;
  public userConfig: any;

  public disableBtn: boolean = true;

  constructor(
    private routerService: RouterService,
    private modalService: ModalService,
    private createRequestService: CreateRequestService,
    private userConfigService: UserConfigService,
    private button: ButtonService
  ) {
    // Router NavigationEnd event
    this.routerService.currentUrl$.subscribe((currentUrl: string) => {
      this.currentUrl = currentUrl;
    });

    this.modalService.modalState$.subscribe(modalState => {
      this.modalState = modalState;
    });

    this.createRequestService.createRequest$.subscribe(createRequest => {
      this.createRequest = createRequest;
    });

    this.createRequestService.editRequestView$.subscribe(editRequest => {
      this.editRequest = editRequest.editRequest;
    });

    this.createRequestService.requestInProgress$.subscribe(inProgress => {
      this.disableBtn = inProgress;
    });

    this.userConfig = this.userConfigService.userconfig;
  }

  public sendRequest(): void {
    const checkRequestMsg: string = localStorage.getItem('checkRequestMsg');
    let popup: boolean;
    if (checkRequestMsg === '1') {
      popup = false;
    } else {
      popup = true;
    }
    this.createRequestService.sendRequest(true, popup);
  }

  public isCurrentUrl(url: string): boolean {
    const regexp: RegExp = new RegExp(`^${url}`);
    return url.length > 1 ? !!this.currentUrl.match(regexp) : this.currentUrl === url;
  }

  ngOnInit() {
    this.button.currentMessage.subscribe(disableBtn => this.disableBtn = disableBtn);
  }

}
