import { Injectable } from '@angular/core';
import { Observable, Subject, of} from 'rxjs';
import { flatMap, map } from 'rxjs/operators';
import cloneDeep from 'lodash-es/cloneDeep';
import { ApiService } from './../api.service';
import {LangService} from '@services/references/lang.service';

@Injectable()
export class ExpenseCategoriesService {

    public expenseCategories: any[];

    private _expenseCategories$: Subject<any> = new Subject();
    private translation: any;

    get expenseCategories$(): Observable<any> {
        return this._expenseCategories$.asObservable();
    }

    constructor(
        private apiService: ApiService,
        private langService: LangService
    ) {
      this.translation = this.langService.lang;
    }

    public getExpenseCategories(): Observable<any> {
        if (this.expenseCategories) {
            this._expenseCategories$.next(this.expenseCategories);
            return of(this.expenseCategories);
        } else {
            return this.apiService.get('References/ExpenseCategories', { auth: false }).pipe(
              map(
                (res) => {
                    res.map(item => {
                      item.Text = this.translation.enum.expenseCategories[item.ID];
                    });
                    this.expenseCategories = res;
                    this._expenseCategories$.next(this.expenseCategories);
                    return res;
                },
                (err) => {
                    console.log('ERROR retrieving expenseCategories', err);
                }
              )
            );
        }
    }

    public getExpenseCategoriesById(id: number): Observable<any> {
        if (this.expenseCategories) {
            return of( cloneDeep(this.expenseCategories).find(el => el.ID === id) );
        } else {
            return this.getExpenseCategories().pipe(
              flatMap(res => {
                return this.getExpenseCategoriesById(id);
              })
            );
        }
    }

}
