import { Directive } from '@angular/core';
import { NG_VALIDATORS, Validator, FormControl } from '@angular/forms';

@Directive({
  selector: '[requiredFile]',
  providers: [
    { provide: NG_VALIDATORS, useExisting: FileSizeValidator, multi: true },
  ]
})
export class FileSizeValidator implements Validator {
  private static maxFileSize: number = 9437184;
  private static maxGlobalFileSize: number = 18874368;

  static validate(c: FormControl): {[key: string]: any} {
    if (c.value && c.value.length > 0) {
      let isOversizeFile: boolean = false;
      let isOversizeGlobalFile: boolean = false;
      const listOversizeFileId: Array<number> = [];
      let globalFileSize: number = 0;
      for (let i = 0; i < c.value.length; i++) {
        const file: any = c.value[i];
        globalFileSize += file.size;
        if (file.size > FileSizeValidator.maxFileSize) {
          listOversizeFileId.push(file.id);
          isOversizeFile = true;
        }
      }
      if (globalFileSize > FileSizeValidator.maxGlobalFileSize) {
        isOversizeGlobalFile = true;
      }
      if (isOversizeFile === true || isOversizeGlobalFile === true) {
        return {'oversizeFile': isOversizeFile, 'oversizeFileId': listOversizeFileId, 'oversizeGlobalFiles': isOversizeGlobalFile};
      }
    }
    return null;
  }

  validate(c: FormControl): {[key: string]: any} {
    return FileSizeValidator.validate(c);
  }
}
