import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { map } from 'rxjs/operators';
import { ApiService } from './api.service';

@Injectable()
export class LeftMenuService {
  private _open$: Subject<boolean>;
  private _companyLogo$: Subject<any>;

  constructor(private apiService: ApiService) {
    this._open$ = new Subject();
    this._companyLogo$ = new Subject();
  }

  // Subject send as observable
  get openMenu$(): Observable<boolean> {
    return this._open$.asObservable();
  }

  // Change the state
  public openMenu(open: boolean): void {
    this._open$.next(open);
  }

  // Get the company logo
  public getCompanyLogoUrl(): Observable<any> {
    return this.apiService
      .get('Profile/CompanyLogo64/', { auth: true, contentType: 'base64' })
      .pipe(
        map(
          res => {
            return res;
          },
          err => {
            console.log('Error getting the Company Picture:', err);
          }
        )
      );
  }
}
