import { Injectable } from '@angular/core';
import { Observable, Subject, of} from 'rxjs';
import { map } from 'rxjs/operators';
import { ApiService } from './api.service';

@Injectable()
export class ExpenseService {

    public openRequests: Array<any> = [];
    public closedRequests: Array<any> = [];

    public expenseEvents: Array<any> = [];

    private _expense$: Subject<any> = new Subject();
    private _closedHelpdesk$: Subject<any> = new Subject();
    private _expenseById$: Subject<any> = new Subject();
    private _expenseMessages$: Subject<any> = new Subject();
    private _closedRequests$: Subject<any> = new Subject();
    private _openRequests$: Subject<any> = new Subject();
    private _expenseEvents$: Subject<any> = new Subject();

    get expense$(): Observable<any> {
        return this._expense$.asObservable();
    }

    get expenseById$(): Observable<any> {
        return this._expenseById$.asObservable();
    }

    get expenseMessages$(): Observable<any> {
        return this._expenseMessages$.asObservable();
    }

    get closedRequests$(): Observable<any> {
        return this._closedRequests$.asObservable();
    }

    get openRequests$(): Observable<any> {
        return this._openRequests$.asObservable();
    }

    get expenseEvents$(): Observable<any> {
        return this._expenseEvents$.asObservable();
    }

    constructor(
        private apiService: ApiService
    ) {}

    public getExpenseEvents(): Observable<any> {
        if (this.expenseEvents.length <= 0) {
            return this.apiService.get('Expense/Events', { auth: true }).pipe(
              map(events => {
                this.expenseEvents = events;
                this._expenseEvents$.next(this.expenseEvents);
                return events;
              })
            );
        } else {
            this._expenseEvents$.next(this.expenseEvents);
            return of(this.expenseEvents);
        }
    }

    public getExpenseRequests(status?: string, offset?: number, limit?: number): void {
        let requests: any[];
        const searchParams: any = {};
        if (status) { searchParams['status'] = status; }
        if (offset) { searchParams['offset'] = offset; }
        if (limit) { searchParams['limit'] = limit; }
        this.apiService.get('Expense/Requests', { auth: true, search: searchParams }).subscribe(
            (res) => {
                requests = res;
                this._expense$.next(requests);
            },
            (err) => {
                console.log('ERROR retrieving expense requests', err);
            }
        );
    }

    public getOpenExpenseRequests(offset?: any, limit?: number): void {
        let requests: any[];
        const searchParams: any = {};
        searchParams['status'] = '0,1,2,3';
        if (offset) { searchParams['offset'] = offset; }
        if (limit) { searchParams['limit'] = limit; }
        this.apiService.get('Expense/Requests', { auth: true, search: searchParams }).subscribe(
            (res) => {
                requests = res;
                this._openRequests$.next(requests);
            },
            (err) => {
                console.log('ERROR retrieving expense requests', err);
            }
        );
    }

    public getClosedExpenseRequests(offset?: number, limit?: number): void {
        let requests: any[];
        const searchParams: any = {};
        searchParams['status'] = '4,5';
        if (offset) { searchParams['offset'] = offset; }
        if (limit) { searchParams['limit'] = limit; }
        this.apiService.get('Expense/Requests', { auth: true, search: searchParams }).subscribe(
            (res) => {
                requests = res;
                this._closedRequests$.next(requests);
            },
            (err) => {
                console.log('ERROR retrieving expense requests', err);
            }
        );
    }

    public getExpenseRequestsById(id: number): void {
        let request: any[];
        this.apiService.get('Expense/Requests/' + id, { auth: true }).subscribe(
            (res) => {
                request = res;
                this._expenseById$.next(request);
            },
            (err) => {
                console.log('ERROR retrieving expense request by ID:' + id + ' ', err);
            }
        );
    }

    public getExpenseRequestsMessages(id: number): void {
        let messages: any[];
        this.apiService.get('Expense/Requests/' + id + '/Messages', { auth: true }).subscribe(
            (res) => {
                messages = res;
                this._expenseMessages$.next(messages);
            },
            (err) => {
                console.log('ERROR retrieving expense request messages by ID:' + id + ' ', err);
            }
        );
    }

    public getAttachmentFile(requestId: number, filename: string): Observable<any> {
        return this.apiService.get('Expense/Requests/' + requestId + '/AttachmentFiles/' + filename + '/', { auth: true }).pipe(
          map(
            (res) => res,
            (err) => {
                console.log('ERROR gettin attachment file:', err);
            }
          )
        );
    }

    public getAttachmentFile64(requestId: number, filename: string): Observable<any> {
        return this.apiService.get('Expense/Requests/' + requestId + '/AttachmentFiles64/' + filename + '/', { auth: true, contentType: 'base64' }).pipe(
          map(
            (res) => {
              // IE < 12
              if (document['documentMode']) {
                res._body = res._body.slice(1, res._body.length - 1);
              }
              return res;
            },
            (err) => {
                console.log('ERROR gettin attachment file:', err);
            }
        )
      );
    }

    public addExpenseRequest(request: any, files?: any): Observable<any> {
        let _body = new FormData();
        if (files && files.length > 0) {
            let count: number = 0;
            files.forEach(file => {
                _body.append('File' + count, file);
                count++;
            });
        }
        _body.append('ExpenseInfo', JSON.stringify(request));
        return this.apiService.post('Expense/Requests', { auth: true, body: _body, contentType: 'multipart' }).pipe(
            map(
                (res) => res,
                (err) => {
                    console.log('ERROR postin new expense request:', err);
                }
            )
        );
    }

    public addExpenseRequestsMessage(id: number, message: string, files?: any): Observable<any> {
        let _body = new FormData();
        if (files && files.length > 0) {
            let count: number = 0;
            files.forEach(file => {
                _body.append('File' + count, file);
                count++;
            });
        }
        _body.append('Message', JSON.stringify({Message: message}));
        return this.apiService.post('Expense/Requests/' + id + '/Messages', { auth: true, body: _body, contentType: 'multipart' }).pipe(
          map(
            (res) => res,
            (err) => {
                console.log('ERROR posting expense request messages' + id + ' ', err);
            }
          )
        );
    }

    private sortRequests(requests: any[]): void {
        this.openRequests = [];
        this.closedRequests = [];
        requests.forEach(request => {
            if (request.Status.ID === 1 || request.Status.ID === 2 || request.Status.ID === 3) {
                this.openRequests.push(request);
            } else {
                this.closedRequests.push(request);
            }
        });
        this._openRequests$.next(this.openRequests);
        this._closedRequests$.next(this.closedRequests);
    }

}
