import { Component, OnInit } from '@angular/core';
import { SwUpdate } from '@angular/service-worker';
import { Router, NavigationEnd, NavigationStart } from '@angular/router';
import { Location } from '@angular/common';
import * as moment from 'moment';
import 'moment/locale/fr';
import 'moment/locale/en-gb';

import { SessionService } from './modules/core/services/session.service';
import { ApiService } from './modules/core/services/api.service';
import { ModalService } from './modules/core/services/modal.service';
import { LangService } from '@services/references/lang.service';



@Component({
  selector: 'itx-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {

  public modalState: boolean;
  public userState: boolean;

  private _sc: string;
  private _firstAuth: string;

  constructor (
    private sessionService: SessionService,
    private apiService: ApiService,
    private router: Router,
    private location: Location,
    private modalService: ModalService,
    private langService: LangService,
    private swUpdate: SwUpdate,
  ) {
    this.apiService.setSessionService(sessionService);

    this.modalService.modalState$.subscribe(modalState => {
      this.modalState = modalState;
    });

    this.sessionService.userState$.subscribe(userState => {
      this.userState = this.sessionService.userIsAuthenticated;
    });

    moment.locale('en');
    moment.updateLocale(langService.getDeviceLocal(), {});
  }

  ngOnInit(): void {
    this.swUpdate.available.subscribe(event => {
      console.log('Service worker: A newer version is now available. Refresh the page now to update the cache');
      window.location.reload(true);
    });
    this.swUpdate.checkForUpdate();

    this.router.events.subscribe((evt) => {
      if (evt instanceof NavigationStart && evt.url === '/login') {
        this._sc = localStorage.getItem('_sc');
        this._firstAuth = localStorage.getItem('_firstAuth');

        if (this._sc === '1') {
          this.router.navigate(['/']);
        } else if (this._firstAuth === '1') {
          this.router.navigate(['/first-auth']);
        }
      }
      if (!(evt instanceof NavigationEnd)) {
        return;
      }
      window.scrollTo(0, 0);
    });

    let currentUrl: string = this.location.path();
    currentUrl = currentUrl.split('?')[0];
    this._sc = localStorage.getItem('_sc');

    if (currentUrl === '/login' && this._sc === '1') {
      this.router.navigate(['/']);
    } else if (currentUrl === '') {
      this.router.navigate(['/login']);
    }

    // fix viewPort size at init, prevent Android to shrink the viewport when the keyboard appear
    this.initViewport();

    this.checkUserSession();

  }

  private initViewport(): void {
    const viewHeight: number = window.innerHeight;
    const viewWidth: number = window.innerWidth;

    const viewport: any = document.querySelector('meta[name=viewport]');
    viewport.setAttribute('content', 'height=' + viewHeight + 'px, width=' + viewWidth + 'px, initial-scale=1, maximum-scale=1, user-scalable=0');
  }

  private checkUserSession(): void {
    if (!this.sessionService.hasSession()) {
      this.sessionService.logout();
    }
  }

}
