import { Observable, throwError } from 'rxjs';
import { catchError, first, map, mergeMap } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { Http, RequestMethod, Request, Headers, Response, URLSearchParams, QueryEncoder, RequestOptions, ResponseContentType } from '@angular/http';
import { Router } from '@angular/router';
import { SessionService } from './session.service';
import { ApiStateService, API_STATE } from './api-state.service';
import {environment} from './../../../../environments/environment';

const hasHttp = (url: string) => url.indexOf('http') > -1;
export type ContentType = 'json' | 'urlencoded' | 'multipart' | 'base64';
type ResponseHandler = <T>(arg: Response) => Observable<T>;
type ErrorHandler = <T>(error: any) => Observable<T>;

export interface ApiOptionsArgs {
    auth?: boolean;
    cachebuster?: boolean;
    offline?: boolean;
    contentType?: ContentType;
    search?: any;
    body?: any;
    loader?: boolean;
}

@Injectable()
export class ApiService {

    private _encoder: ApiEncoder = new ApiEncoder();
    private _sessionService: SessionService;
    private _queue: Set<string> = new Set();

    private ApiBaseURL = environment.apiUrl;

    constructor(
        private http: Http,
        private apiStateService: ApiStateService,
        private router: Router
    ) {
    }

    public setSessionService(any: SessionService): void {
        this._sessionService = any as SessionService;
    }

    get(res: string, args?: ApiOptionsArgs): Observable<any> {
        return this.request(res, args, RequestMethod.Get);
    }

    post(res: string, args?: ApiOptionsArgs): Observable<any> {
        return this.request(res, args, RequestMethod.Post);
    }

    put(res: string, args?: ApiOptionsArgs): Observable<any> {
        return this.request(res, args, RequestMethod.Put);
    }

    delete(res: string, args?: ApiOptionsArgs): Observable<any> {
        return this.request(res, args, RequestMethod.Delete);
    }

    private getURL(res: string): string {
        return hasHttp(res) ? res : `${this.ApiBaseURL}${res}`;
    }

    private buildRequest(res: string, args: ApiOptionsArgs, method: RequestMethod): Request {
        let url: string = this.getURL(res);
        let externalURL: boolean = url.indexOf('http') === 0 && url.indexOf(this.ApiBaseURL) < 0; // is an external URL ?
        let cachebuster: boolean = (args.cachebuster !== undefined) ? args.cachebuster : false;
        let needsAuthentication: boolean = (args.auth !== undefined) ? args.auth : true;
        let search: URLSearchParams = new URLSearchParams('', this._encoder);
        let headers: Headers = new Headers();
        let body: any = undefined;
        let responseType = ResponseContentType.Json;

        if (!externalURL) {
            if (needsAuthentication && this._sessionService) {
                let accessToken = this._sessionService.getAccessToken();
                accessToken = 'SAML ' + accessToken;
                headers.set('Authorization', accessToken);
            }
        }

        if (cachebuster) { search.set('c', new Date().getTime().toString()); }

        if (args.search !== undefined && args.search) {
            Object.keys(args.search).map(name => {
                search.set(name, args.search[name].toString());
            });
        }

        if (args.body !== undefined && args.body && method !== RequestMethod.Get) {
            if (args.contentType === 'urlencoded') {
                let params = new URLSearchParams('', this._encoder);
                Object.keys(args.body).map(name => {
                    if (args.body[name] !== null && args.body.hasOwnProperty(name) && typeof args.body[name] !== 'undefined') {
                        params.set(name, args.body[name].toString());
                    }
                });
                body = params.toString();
                headers.set('Content-Type', 'application/x-www-form-urlencoded; charset=utf-8');
            } else if (args.contentType === 'multipart') {
                body = args.body;
                headers.delete('Content-Type');
                headers.set('Mime-Type', 'multipart/form-data');
            } else {
                body = JSON.stringify(args.body);
                headers.set( 'Content-Type', 'application/json; charset=utf-8' );
            }
        }

        if (res === 'Helpdesk/AttachmentFile') {
            headers.set('Content-Type', 'application/json');
            headers.set('Accept', 'image/*,application/pdf');
            responseType = ResponseContentType.Blob;
        }
        return new Request({ url: url, method: method, headers: headers, params: search, body: body, responseType: responseType });
    }

    private request(resource: string, args: ApiOptionsArgs = {}, method: RequestMethod = RequestMethod.Get): Observable<any> {

        let req: Request = this.buildRequest(resource, args, method);
        let enqueueable: boolean = (args.loader !== undefined) ? args.loader : true;

        let key = `${resource}.${new Date().getTime()}`;

        if (enqueueable) { this.enqueue(key); }
        let dequeue = () => this.dequeue(key);

        let onResponse: ResponseHandler;
        let onError;

        switch (method) {
            case RequestMethod.Get:
                onResponse = (response) => {
                    if (enqueueable) {
                        dequeue();
                    }
                    if (response.json && args.contentType !== 'base64') {
                        return response.json();
                    } else { return response; }
                };
                onError = (error) => {
                    if (enqueueable) { dequeue(); }
                    switch (error.status) {
                        case 0:
                            return throwError(error);
                        case 401:
                            if (!this._sessionService.refreshState) {
                                this._sessionService.refresh().subscribe();
                                return this._sessionService.refresh$.pipe(
                                  first(),
                                  mergeMap(refreshState => {
                                      if (!refreshState) {
                                          return this.http.request(this.buildRequest(resource, args, method)).pipe(
                                            map(onResponse),
                                            catchError(onError)
                                          );
                                      }
                                  })
                                );
                            } else {
                                return this._sessionService.refresh$.pipe(
                                  first(),
                                  mergeMap(refreshState => {
                                    if (!refreshState) {
                                        return this.http.request(this.buildRequest(resource, args, method)).pipe(
                                          map(onResponse),
                                          catchError(onError)
                                      );
                                    }
                                  })
                                );
                            }
                        case 403:
                            if (error.json().Code != 403003) {
                                this._sessionService.logout().subscribe(res => {
                                    this.router.navigate(['/login']);
                                });
                            }
                            return throwError(error.json());
                        default:
                            return throwError(error.json());
                    }
                };
                break;
            case RequestMethod.Post:
            case RequestMethod.Put:
            case RequestMethod.Delete:
                onResponse = response => {
                    if (enqueueable) { dequeue(); }
                    return response.json();
                };
                onError = error => {
                    if (enqueueable) { dequeue(); }
                    if (error.status === 0) { this.apiStateService.currentApiStateSet(API_STATE.UNREACHABLE); }
                    switch (error.status) {
                        case 0:
                            this.apiStateService.currentApiStateSet(API_STATE.UNREACHABLE);
                            return throwError((error as Response).json());
                        case 401:
                            if (error.json().Code == 401001) {
                                this._sessionService.logout().subscribe(res => {
                                    this.router.navigate(['/login']);
                                });
                            }
                            if (!this._sessionService.refreshState && args.auth) {
                                this._sessionService.refresh().subscribe();
                                return this._sessionService.refresh$.pipe(
                                  first(),
                                  mergeMap(refreshState => {
                                    if (!refreshState) {
                                        return this.http.request(this.buildRequest(resource, args, method)).pipe(
                                          map(onResponse),
                                          catchError(onError)
                                        );
                                    }
                                  })
                                );
                            } else if (args.auth) {
                                return this._sessionService.refresh$.pipe(
                                  first(),
                                  mergeMap(refreshState => {
                                    if (!refreshState) {
                                        return this.http.request(this.buildRequest(resource, args, method)).pipe(
                                          map(onResponse),
                                          catchError(onError)
                                        );
                                    }
                                  })
                                );
                            } else {
                                return throwError((error as Response).json());
                            }
                        case 403:
                            if (error.json().Code != 403003) {
                                this._sessionService.logout().subscribe(res => {
                                    this.router.navigate(['/login']);
                                });
                            }
                            return throwError((error as Response).json());
                        default:
                            if (error instanceof Response) {
                                return throwError((error as Response).json());
                            } else { return throwError(error); }
                    }
                };
        }

        return this.http.request(req).pipe(
          map(onResponse),
          catchError(onError)
        );
    }

    private enqueue(op: string): void {
        this._queue.add(op);
        this.apiStateService.currentApiStateSet(API_STATE.ON_OPERATIONS);
    }

    private dequeue(op: string): void {
        this._queue.delete(op);
        if (this._queue.size === 0) {
            this.apiStateService.currentApiStateSet(API_STATE.IDLE);
        }
    }

}

/**
 * Custom query encoder
 * This encoder will encode all values with encodeURIComponent
 * because Angular's default QueryEncoder doesn't encode some characters.
 *
 * @class ApiEncoder
 * @extends {QueryEncoder}
 */
class ApiEncoder extends QueryEncoder {

    encodeValue(value: string): string {
        return encodeURIComponent(value);
    }

}
