import { Injectable } from '@angular/core';
import { Observable, from } from 'rxjs';
import { map } from 'rxjs/operators';
import { ApiService } from './../api.service';

@Injectable()
export class LangService {

    public lang: any;
    public userLang: any;
    private debugLangLocal: boolean;
    private debugDeviceLang: string;

    constructor(private apiService: ApiService) {
        this.debugLangLocal = false;
        if (localStorage.getItem('debugLangLocal')) {
            this.debugLangLocal = true;
        }
        this.debugDeviceLang = null;
        if (localStorage.getItem('debugDeviceLang')) {
            this.debugDeviceLang = localStorage.getItem('debugDeviceLang');
        }
    }

    public getLangLocal(): Observable<any> {
        const local: string = this.getDeviceLocal();
        const promiseLang: Promise<Response> = fetch('./../../../../assets/lang/' + local + '.json')
            .then(data => {
                return data.json();
            });
        return from(promiseLang).pipe(
            map(res => {
                this.lang = res;
                return res;
            })
        );
    }

    public getDeviceLocal(): string {
        if (!this.userLang) {
            if (localStorage.getItem('userLang')) {
                this.userLang = localStorage.getItem('userLang');
            } else {
                this.userLang = window.navigator.language.substring(0, 2);
            }
        }
        const local: string = this.userLang;
        if (local === 'fr' || local === 'FR') {
            return 'fr';
        }
        return 'en';
    }

    public setUserLang(lang: string): boolean {
        this.userLang = lang;
        this.lang = lang;
        return true;
    }

    public getLang(isLoggedIn?: boolean): Observable<any> {
        const isAuthenticated: boolean = isLoggedIn === undefined ? false : true;
        const local: string = this.getDeviceLocal();
        let respLang: any = null;
        if (this.debugLangLocal) {
            respLang = this.getLangLocal();
        } else {
            respLang = this.apiService.get('References/LanguageResources/' + local, { auth: isAuthenticated }).pipe(
                map(res => {
                    this.lang = res;
                    return res;
                })
            );
        }
        return respLang;
    }

    public onLogOut(): void {
        this.lang = null;
        this.userLang = null;
    }

}
