import { Injectable } from '@angular/core';
import { Observable ,  Subject ,  BehaviorSubject ,  combineLatest } from 'rxjs';
import cloneDeep from 'lodash-es/cloneDeep';
import * as moment from 'moment';

import { ApiService } from './api.service';
import { ExpenseService } from './expense.service';
import { HelpdeskService } from './helpdesk.service';
import { ProfileService } from './profile.service';

@Injectable()
export class RequestsService {

    public profileRequests: any[];
    public expenseRequests: any[];
    public helpdeskRequests: any[];

    public allRequests: any[] = [];
    public allOpenRequests: any[] = [];
    public openProfileHelpdeskRequests: any[] = [];
    public closedProfileHelpdeskRequests: any[] = [];

    private _allRequests$: Subject<any> = new Subject();
    private _allOpenRequests$: Subject<any> = new Subject();
    private _openProfileHelpdeskRequests$: Subject<any> = new Subject();
    private _closedProfileHelpdeskRequests$: Subject<any> = new Subject();

    private combinedAll: Observable<any>;
    private combinedAllOpen: Observable<any>;
    private combinedOpenProfileHelpdesk: Observable<any>;
    private combinedClosedProfileHelpdesk: Observable<any>;

    get allRequests$(): Observable<any> {
        return this._allRequests$.asObservable();
    }

    get allOpenRequests$(): Observable<any> {
        return this._allOpenRequests$.asObservable();
    }

    get openProfileHelpdeskRequests$(): Observable<any> {
        return this._openProfileHelpdeskRequests$.asObservable();
    }

    get closedProfileHelpdeskRequests$(): Observable<any> {
        return this._closedProfileHelpdeskRequests$.asObservable();
    }

    constructor(
        private apiService: ApiService,
        private expenseService: ExpenseService,
        private helpdeskService: HelpdeskService,
        private profileService: ProfileService
    ) {
        // ALL REQUESTS
        this.combinedAll = combineLatest(
            this.profileService.profileRequests$,
            this.expenseService.expense$,
            this.helpdeskService.helpdesk$
        );

        // ALL REQUESTS OPEN
        this.combinedAllOpen = combineLatest(
            this.profileService.openProfileRequests$,
            this.expenseService.openRequests$,
            this.helpdeskService.openRequests$
        );

        // OPEN PROFILE AND HELPDESK
        this.combinedOpenProfileHelpdesk = combineLatest(
            this.profileService.openProfileRequests$,
            this.helpdeskService.openRequests$
        );

        // CLOSED PROFILE AND HELPDESK
        this.combinedClosedProfileHelpdesk = combineLatest(
            this.profileService.closedProfileRequests$,
            this.helpdeskService.closedRequests$
        );

        this.combinedAll.subscribe( ([profileRequests, expenseRequests, helpdeskRequests]) => {
            this.profileRequests = profileRequests;
            this.expenseRequests = expenseRequests;
            this.helpdeskRequests = helpdeskRequests;
            this.allRequests = this.profileRequests.concat(this.expenseRequests, this.helpdeskRequests);
            this.allRequests = this.sortRequest(this.allRequests);
            this._allRequests$.next(cloneDeep(this.allRequests));
        });

        this.combinedAllOpen.subscribe( ([profileOpenRequests, expenseOpenRequests, helpdeskOpenRequests]) => {
            this.allOpenRequests = profileOpenRequests.concat(expenseOpenRequests, helpdeskOpenRequests);
            this.allOpenRequests = this.sortRequest(this.allOpenRequests);
            this._allOpenRequests$.next(cloneDeep(this.allOpenRequests));
        });

        this.combinedOpenProfileHelpdesk.subscribe( ([profileRequests, helpdeskRequests]) => {
            this.openProfileHelpdeskRequests = profileRequests.concat(helpdeskRequests);
            if (this.openProfileHelpdeskRequests.length > 1) {
                this.openProfileHelpdeskRequests = this.sortRequest(this.openProfileHelpdeskRequests);
            }
            this._openProfileHelpdeskRequests$.next(cloneDeep(this.openProfileHelpdeskRequests));
        });

        this.combinedClosedProfileHelpdesk.subscribe( ([profileRequests, helpdeskRequests]) => {
            this.closedProfileHelpdeskRequests = profileRequests.concat(helpdeskRequests);
            if (this.closedProfileHelpdeskRequests.length > 1) {
                this.closedProfileHelpdeskRequests = this.sortRequest(this.closedProfileHelpdeskRequests);
            }
            this._closedProfileHelpdeskRequests$.next(cloneDeep(this.closedProfileHelpdeskRequests));
        });
    }

    private sortRequest(requests): any {
        return requests.sort(this.sortFunction);
    }

    private sortFunction(a, b): number {
        if (a.Status.ID === 2 && b.Status.ID !== 2) {
            return -1;
        } else if (a.Status.ID !== 2 && b.Status.ID === 2) {
            return 1;
        } else if (a.Status.ID === 2 && b.Status.ID === 2) {
            return 0;
        }

        if (moment(a.Date).isBefore(b.Date)) {
            return 1;
        } else if (moment(a.Date).isAfter(b.Date)) {
            return -1;
        } else {
            return 0;
        }
    }

    public getAllRequests(status?: string, offset?: number, limit?: number): void {
        this.allRequests = [];
        this.profileService.getProfileRequests();
        this.expenseService.getExpenseRequests();
        this.helpdeskService.getHelpdeskRequests();
    }

    public getAllOpenRequests(status?: string, offset?: number, limit?: number): void {
        this.allOpenRequests = [];
        this.profileService.getOpenProfileRequests();
        this.expenseService.getOpenExpenseRequests();
        this.helpdeskService.getOpenHelpdeskRequests();
    }

    public getOpenProfileHelpdeskRequests(status?: string, offset?: number, limit?: number): void {
        this.openProfileHelpdeskRequests = [];
        this.profileService.getOpenProfileRequests();
        this.helpdeskService.getOpenHelpdeskRequests();
    }

    public getClosedProfileHelpdeskRequests(status?: string, offset?: number, limit?: number): void {
        this.closedProfileHelpdeskRequests = [];
        this.profileService.getClosedProfileRequests();
        this.helpdeskService.getClosedHelpdeskRequests();
    }
}
