import { Component, Input, OnInit } from '@angular/core';
import { LangService } from '@services/references/lang.service';
import { FormControl, FormGroup, FormBuilder } from '@angular/forms';
import cloneDeep from 'lodash-es/cloneDeep';
import { Location } from '@angular/common';

@Component({
    selector: 'itx-dropdown-select',
    templateUrl: './dropdown-select.component.html',
    styleUrls: ['./dropdown-select.component.scss']
})
export class DropdownSelectComponent implements OnInit {

    public parent: FormGroup;
    public control: FormControl;
    @Input() public list: any;

    public translation;
    public LangList: any[];
    public defaultLang: string;
    public currentVal: string;

    constructor(
        private langService: LangService,
        private location: Location,
        private fb: FormBuilder
    ) {
        this.translation = this.langService.lang;
    }

    ngOnInit(): void {
        this.getLangList();
        this.setDefaultLang();
    }

    public getLangList(): void {
        this.LangList = [{ 'Name': 'English', 'langvalue': 'en' },
        { 'Name': 'French', 'langvalue': 'fr' }];
    }

    public pageRefresh(): void {
        location.reload();
    }

    public setDefaultLang(): void {
        this.defaultLang = this.langService.getDeviceLocal();
        this.setCurrentVal(this.defaultLang);
    }

    public setCurrentVal(langvalue: string): void {
        this.currentVal = this.LangList.find(function(lang: any): any {
            return lang.langvalue === langvalue;
        }).Name;
    }

    public onChange(value: any): void {
        this.langService.setUserLang(value);
        this.defaultLang = value;
        this.setCurrentVal(this.defaultLang);
        this.langService.getLang(true).subscribe(res => {
            this.translation = this.langService.lang;
            this.pageRefresh();
            localStorage.setItem('userLang', value);
        });
    }

    // public getControlValue(value: any): string {
    //     const newVal = cloneDeep(this.list);
    //     if (newVal) {
    //         return newVal.Name;
    //     } else {
    //         return 'None';
    //     }
    // }

    public isCurrentLanguage(language: string): boolean {
        return language === this.defaultLang ? true : false;
    }
}
