import { Injectable } from '@angular/core';
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';
import { Observable , Subject, combineLatest } from 'rxjs';

@Injectable()
export class RouterService {

    private _previousUrl: string;
    private _currentUrl: string;
    private _routerCurrentUrl$: Subject<string>;

    private combinedObs: Observable<any>;

    constructor(
      private router: Router,
      private activatedRoute: ActivatedRoute
    ) {
        // Set Subjects
        this._routerCurrentUrl$ = new Subject();

        this.combinedObs = combineLatest(
            router.events,
            activatedRoute.queryParams
        );
        this._currentUrl = this.router.url;
        this.combinedObs.subscribe( ([event, params]) => {
            if (event instanceof NavigationEnd) {
                if (!params['skipLocationChange']) {
                    this._previousUrl = this._currentUrl;
                    this._currentUrl = event.url;
                    this._routerCurrentUrl$.next(this._currentUrl);
                }
            }
        });
    }

    get currentUrl$(): Observable<any> {
        return this._routerCurrentUrl$.asObservable();
    }

    public goBack(): void {
        const route = this._previousUrl !== this._currentUrl ? this._previousUrl : '/';
        if (route.match('(modal:new-requests)')) {
            const newRoute = route.replace('(modal:new-requests)', '');
            this.router.navigate([newRoute], { skipLocationChange: true, queryParams: { skipLocationChange: true } }).then(res => {
                this.router.navigate(['', { outlets: { modal: 'new-requests' } }]);
            });
        } else {
            this.router.navigate([route]);
        }
    }
}
