import { Observable, Subject, BehaviorSubject, throwError, of } from 'rxjs';
import { Injectable } from '@angular/core';
import { ApiService } from './api.service';
import * as CryptoJS from 'crypto-js';
import { generateUUID } from './../../shared/uuid';
import { UserService } from './user.service';
import { catchError, map } from 'rxjs/operators';
import { UserConfigService } from './references/user-configuration.service';
import { LangService } from './references/lang.service';

export class Credentials { username: string; password: string; }

const M: number = 999999;
const rand = (a: number, b: number): number => Math.floor(Math.random() * (Math.floor(b) - Math.ceil(a))) + Math.ceil(a);
const seal = (x: number): number => M * rand(9, 99) + x;
const reveal = (x: number): number => { const m: number = x % M; return (m > M / 2) ? m - M : m; };

export class SessionArgs {
    UserInfo: {
        AccountName: string | number;
        EmailAddress: string;
        PrincipalName: string;
        DisplayName: string;
    };
    Token: string;
}

export class SessionData {
    public data: SessionArgs;

    static merge(session: SessionData, args: SessionArgs): SessionData {
        const data: SessionArgs = Object.assign({}, session.data, args);
        return new SessionData(data);
    }

    constructor(args?: SessionArgs) {
        if (args) {
            this.data = args;
        }
    }

    public get id(): string | number { return this.data.UserInfo.AccountName; }
    public get accessToken(): string { return this.data.Token; }

    public isValid(): boolean {
        return (this.accessToken !== null);
    }

    public serialize(): string {
        return JSON.stringify(this.data);
    }

}

export enum UserState { UNKNOWN = -1, AUTHENTICATED }

@Injectable()
export class SessionService {
    private _sessionData: SessionData;
    private _userState$: BehaviorSubject<UserState> = new BehaviorSubject(UserState.UNKNOWN);

    private _userInfos$: Subject<SessionData> = new Subject();
    private _refresh$: Subject<boolean> = new Subject();

    private isLocalStorageSupported: boolean;

    public deviceID: string;
    public refreshState: boolean;

    constructor(
        private api: ApiService,
        private userService: UserService,
        private userConfigService: UserConfigService,
        private langService: LangService,
    ) {
        this.refreshState = false;
        this.isLocalStorageSupported = true;
        this.isLocalStorageSupported = this.checkLocalStorageSupported();
        this.checkUserState();

        this.deviceID = localStorage.getItem('deviceID');

        if (!this.deviceID) {
            this.changeDeviceID();
        }
    }

    get userIsAuthenticated(): boolean {
        return this._userState$.getValue() === UserState.AUTHENTICATED;
    }

    get userState$(): Observable<UserState> {
        return this._userState$.asObservable();
    }

    get userInfos$(): Observable<SessionData> {
        return this._userInfos$.asObservable();
    }

    get refresh$(): Observable<boolean> {
        return this._refresh$.asObservable();
    }

    private checkLocalStorageSupported(): boolean {
        try {
            localStorage.setItem('supported', '1');
            localStorage.removeItem('supported');
            return true;
        } catch (e) {
            return false;
        }
    }

    public changeDeviceID(): string {
        this.deviceID = generateUUID();
        localStorage.setItem('deviceID', this.deviceID);

        return this.deviceID;
    }

    public getAccessToken(): string {
        return this._sessionData ? this._sessionData.accessToken : '';
    }

    public sendEmailOTP(): Observable<any> {
        const credentials: Credentials = this.restoreCredentials();
        if (!credentials) {
            return of(false);
        }
        const body: any = {
            UserName: credentials.username,
            Password: credentials.password,
            DeviceID: this.deviceID
        };

        return this.api.post('Authentication/OTP', { auth: false, body }).pipe(
            map(res => res),
            catchError(err => throwError(err))
        );
    }

    public resetPassword(formSearch: any): Observable<any> {
        return this.api.post('Authentication/ResetPassword', { auth: false, search: formSearch }).pipe(
            map(res => res),
            catchError(err => throwError(err))
        );
    }

    public changePassword(formValue: any): Observable<any> {
        const _body: any = {
            Credentials: {
                UserName: formValue.UserName,
                Password: formValue.Password,
                DeviceID: this.deviceID,
            },
            NewPassword: formValue.NewPassword,
        };
        return this.api.post('Authentication/ChangePassword', { auth: false, body: _body }).pipe(
            map(res => res),
            catchError(err => throwError(err))
        );
    }

    public registerDevice(validOTP: number): Observable<any> {
        const credentials: Credentials = this.restoreCredentials();
        if (!credentials) {
            return of(false);
        }
        const body: any = {
            UserName: credentials.username,
            Password: credentials.password,
            DeviceID: this.deviceID
        };
        const search: any = {
            OTP: validOTP
        };

        return this.api.post('Authentication/Device', { auth: false, body, search }).pipe(
            map(res => {
                this.saveSessionData(credentials, res);
                this._userState$.next(UserState.AUTHENTICATED);
                this._userInfos$.next(this.sessionData);
                localStorage.setItem('_sc', '1');
                return res;
            }),
            catchError(err => throwError(err))
        );
    }

    public authenticate(credentials: Credentials): Observable<SessionData> {
        const body: any = {
            UserName: credentials.username,
            Password: credentials.password,
            DeviceID: this.deviceID
        };

        return this.api.post('Authentication/Token', { auth: false, body }).pipe(
            map(res => {
                this.saveSessionData(credentials, res);
                this._userState$.next(UserState.AUTHENTICATED);
                this._userInfos$.next(this.sessionData);
                localStorage.setItem('_sc', '1');
                return res;
            }),
            catchError(err => throwError(err))
        );
    }

    public saveSessionData(credentials: Credentials, json?) {
        const session: SessionData = json ? new SessionData(json as SessionArgs) : new SessionData();
        this.sessionData = session;
        const key: number = this.convertToNumber(this.sessionData.id);
        const enc = CryptoJS.AES.encrypt(JSON.stringify(credentials), key.toString());
        sessionStorage.setItem('_c', enc.toString());
        sessionStorage.setItem('_k', `${seal(key)}`);
        return session;
    }

    public refresh(): Observable<any> {
        this.refreshState = true;
        this._refresh$.next(this.refreshState);
        const credentials: Credentials = this.restoreCredentials();
        if (credentials) {
            return this.authenticate(credentials).pipe(
                map(res => {
                    this.refreshState = false;
                    // wait for the API, if you send the new token before 500ms the server will respond with 401
                    setTimeout(() => {
                        this._refresh$.next(this.refreshState);
                    }, 550);
                    return res;
                })
            );
        } else {
            return of(false);
        }
    }

    public logout(): Observable<any> {

        const _logout: () => Observable<boolean> = () => {
            this._userState$.next(UserState.UNKNOWN);
            this.sessionData = undefined;
            this.deviceID = localStorage.getItem('deviceID');
            const onBoarding: string = localStorage.getItem('onboarding');
            const checkPwaMsg: string = localStorage.getItem('checkPwaMsg');
            const fallbackLanguage: string = localStorage.getItem('userLang');
            localStorage.clear();
            localStorage.setItem('deviceID', this.deviceID);
            localStorage.setItem('checkPwaMsg', checkPwaMsg);
            localStorage.setItem('userLang', fallbackLanguage ? fallbackLanguage : 'en');
            if (onBoarding !== null) {
                localStorage.setItem('onboarding', onBoarding);
            }
            this.userConfigService.onLogOut();
            this.langService.onLogOut();
            return of(true);
        };

        return _logout();
    }

    public getUserInfos() {
        const session: SessionData = this.restoreSession();
        if (!session) {
            return false;
        }
        this._userInfos$.next(session);
    }

    public hasSession(): boolean {
        return sessionStorage.getItem('session') !== null;
    }

    private hasCredentials(): boolean {
        return (sessionStorage.getItem('_c') !== undefined && sessionStorage.getItem('_k') !== undefined);
    }

    private get sessionData(): SessionData {
        return this._sessionData;
    }

    private set sessionData(sessionData: SessionData) {
        this._sessionData = sessionData;
        if (sessionData !== undefined) {
            sessionStorage.setItem('session', this._sessionData.serialize());
        } else {
            sessionStorage.removeItem('session');
        }
    }

    private restoreSession(): SessionData {
        let str: string = sessionStorage.getItem('session');
        if (!str && this._sessionData) {
            str = this._sessionData.serialize();
        }
        return str ? new SessionData(JSON.parse(str)) : undefined;
    }

    private checkUserState(): void {
        const session: SessionData = this.restoreSession();
        if (!session) {
            this._userState$.next(UserState.UNKNOWN);
        } else {
            this.sessionData = session;
            this._userInfos$.next(this.sessionData);
        }
    }

    public restoreCredentials(): Credentials {
        let credentials: Credentials = null;
        const cipher: string = sessionStorage.getItem('_c');
        const key: number = reveal(parseInt(sessionStorage.getItem('_k'), 10));
        if (cipher && key) {
            const bytes: CryptoJS.DecryptedMessage = CryptoJS.AES.decrypt(cipher.toString(), key.toString());
            credentials = JSON.parse(bytes.toString(CryptoJS.enc.Utf8));
        }
        return credentials;
    }

    private convertToNumber(id: any): number {
        if (id !== parseInt(id, 10)) {
            let newId: number = 0;
            id.split('').forEach(char => {
                newId += char.charCodeAt(0);
            });
            return newId;
        } else {
            return parseInt(id, 10);
        }
    }

}
