import { Injectable } from '@angular/core';
import { Observable, Subject, of} from 'rxjs';
import { map } from 'rxjs/operators';
import { Countrie } from '@app/modules/shared/models/countrie';
import { ApiService } from './../api.service';
import { LangService } from '@services/references/lang.service';

@Injectable()
export class CountriesService {
    public countries: Countrie[];
    private _countries$: Subject<any> = new Subject();
    private translation: any;

    get countries$(): Observable<any> {
        return this._countries$.asObservable();
    }

    constructor(
        private apiService: ApiService,
        private langService: LangService
    ) {
      this.translation = this.langService.lang;
    }

    public getCountries(): Observable<any> {
        if (this.countries) {
            this._countries$.next(this.countries);
            return of(this.countries);
        } else {
           const searchParams: any = {};
           searchParams['languageCode'] = this.langService.userLang;
           return this.apiService.get('References/Countries', { auth: true, search : searchParams }).pipe(
              map(
                (res) => {
                    res.map(item => {
                      item.Text = this.translation.enum.countries[item.ID];
                    });
                    this.countries = res;
                    this._countries$.next(res);
                    return res;
                },
                (err) => {
                    console.log('ERROR retrieving countrie', err);
                }
              )
            );
        }
    }
}
