import { Injectable } from '@angular/core';
import { Observable ,  Subject ,  BehaviorSubject } from 'rxjs';

import { ApiService } from './../api.service';
import {LangService} from '@services/references/lang.service';

@Injectable()
export class MaritalStatusService {

    public maritalStatus: any[];

    private _maritalStatus$: Subject<any> = new Subject();
    private translation: any;

    get maritalStatus$(): Observable<any> {
        return this._maritalStatus$.asObservable();
    }

    constructor(
        private apiService: ApiService,
        private langService: LangService,
    ) {
      this.translation = this.langService.lang;
    }

    public getMaritalStatus(): void | boolean {
        if (this.maritalStatus) {
            this._maritalStatus$.next(this.maritalStatus);
            return false;
        }
        this.apiService.get('References/MaritalStatus', { auth: false }).subscribe(
            (res) => {
                res.map(item => {
                  item.Text = this.translation.enum.maritalStatus[item.ID];
                });
                this.maritalStatus = res;
                this._maritalStatus$.next(this.maritalStatus);
            },
            (err) => {
                console.log('ERROR retrieving maritalStatus', err);
            }
        );
        }

}
