import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { map } from 'rxjs/operators';
import { ApiService } from './api.service';

@Injectable()
export class SuperiorService {

    public superior: any;

    private _superior$: Subject<any> = new Subject();

    get superior$(): Observable<any> {
        return this._superior$.asObservable();
    }

    constructor(
        private apiService: ApiService
    ) {}

    public getSuperior(): void {
        this.apiService.get('Profile/Superior', { auth: true }).subscribe(superior => {
            this.superior = superior;
            this._superior$.next(this.superior);
        });
    }

    public superiorRequest(request): Observable<any> {
        return this.apiService.post('Profile/Superior', { auth: true, body: request }).pipe(
            map(
                (res) => res,
                (err) => {
                    console.log('ERROR postin new superior request:', err);
                }
            )
        );
    }

}
