import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { trigger, state, style, animate, transition } from '@angular/animations';

import { validateEmail } from './../../../shared/utils';
import { SessionService } from './../../services/session.service';
import { PopupService, PopupOptions } from './../../services/popup.service';
import { LangService } from './../../services/references/lang.service';

@Component({
  selector: 'itx-renew-password',
  templateUrl: './renew-password.component.html',
  styleUrls: ['./renew-password.component.scss'],
  animations: [
    trigger('fade', [
        state('void', style({ opacity: 0 })),
        transition('void => *', animate('1s 150ms ease-out', style({ opacity: 1 })))
    ]),
    trigger('fade2', [
        state('void', style({ opacity: 0 })),
        transition('void => *', animate('350ms 350ms ease-out', style({ opacity: 1 }))),
        transition('* => void', animate('350ms ease-out', style({ opacity: 0 })))
    ]),
    trigger('slideDown', [
        state('false', style({ transform: 'translateY(0%)' })),
        state('true', style({ transform: 'translateY(8%)' })),
        transition('false => true', animate('350ms ease-out', style({ transform: 'translateY(8%)' }))),
        transition('true => false', animate('350ms 350ms ease-out', style({ transform: 'translateY(0)' })))
    ])
  ]
})
export class RenewPasswordComponent {
    public firstStep: boolean = true;

    public identifiant: FormControl;
    public email: FormControl;
    public renewPasswordForm: FormGroup;

    public onLoad: boolean;

    public translation;

    constructor(
        public fb: FormBuilder,
        private router: Router,
        private sessionService: SessionService,
        private popupService: PopupService,
        private langService: LangService
    ) {
        this.onLoad = false;
        this.identifiant = new FormControl(null, [ Validators.required ]);
        this.email = new FormControl(null, [ Validators.required ], [ validateEmail.bind(this.email) ]);
        this.renewPasswordForm = this.fb.group({
            Username: this.identifiant,
            EmailAddress: this.email
        });

        this.translation = this.langService.lang;
    }


    public renewPassword(): void {
        this.onLoad = true;
        this.sessionService.resetPassword(this.renewPasswordForm.value).subscribe(
            (res) => {
                const popupOptions: PopupOptions = {
                    title: this.translation.resetPassword.popIn.title,
                    text: this.translation.resetPassword.popIn.resetPasswordSuccess,
                    bgState: false,
                    arrow: false,
                    timer: 4000,
                    position: {
                        bottom: '4vh'
                    }
                };
                this.popupService.setPopupState(true, popupOptions);
                this.router.navigate(['/login']);
            },
            (err) => {
                this.onLoad = false;
                console.log('ERROR RESET PASSWORD', err);
                let errTxt: string;
                switch(err.Code) {
                    case 401004:
                    errTxt = this.translation.resetPassword.popIn.emailInvalid;
                    break;
                    case 401003:
                    errTxt = this.translation.resetPassword.popIn.usernameInvalid;
                    break;
                    case 500004:
                    errTxt = this.translation.resetPassword.popIn.resetPasswordFailed;
                    break;
                }
                const popupOptions: PopupOptions = {
                    title: this.translation.resetPassword.popIn.title,
                    text: errTxt,
                    bgState: false,
                    arrow: false,
                    timer: 4000,
                    position: {
                        bottom: '4vh'
                    }
                };
                this.popupService.setPopupState(true, popupOptions);
            }
        );
    }
}
